.boost-confidence {
  padding-top: 70px;
  padding-bottom: 60px;
}

.center-heading {
  text-align: center;
  max-width: 500px;
  margin: auto;
  padding-bottom: 30px;
}

.grey-bg-grid {
  .grey-bg {
    .number-wrapper {
      padding: 0 20px;
    }

    .heading-wrapper {
      padding: 30px 20px 0 20px;
    }
  }

  .plan-items {
    .red-text {
      margin-top: 60px;
      margin-bottom: 15px;
      color: $brand-primary;
    }

    p {
      font-size: 0.8rem;
    }

    &.last-item {
      background-color: #fff;
      padding-top: 1px;
    }
  }
}



