.block-menu {
  display: flex;
  margin-bottom: 1rem;
  margin-top: 0;
  padding-left: 0;

  a {
    display: flex;
    flex-direction: row;
    padding: 0 1rem;
    align-items: center;
    color: inherit;

    &:hover {
      text-decoration: none;
      color: $secondary;
    }
  }

  li {
    display: flex;
  }

  &__icon {
    display: block;
    width: 80px;
    height: 80px;
    margin-right: 1rem;

    svg {
      max-width: 100%;
      height: auto;
    }
  }

  &__title {
    flex: none;
    font-family: $headings-font-family;
    font-size: $h4-font-size;
    line-height: 1;

    small {
      font-size: 1rem;
      font-family: $font-family-base;
      margin-top: 0.5rem;
      display: block;
    }
  }

  @include media-breakpoint-up(lg) {
    li {
      justify-content: center;
      width: calc(100% / 3);

      &:not(:first-child) {
        border-left: 1px solid $black;
      }
    }
  }

  @include media-breakpoint-down(lg) {
    &__icon {
      width: 50px;
      height: 50px;
    }

    &__title {
      flex: none;
      font-size: $h4-font-size;
    }
  }

  @include media-breakpoint-down(md) {
    justify-content: space-between;
    margin-bottom: 1rem;

    &__icon {
      width: 40px;
      height: 40px;
    }

    a {
      padding: 0;
    }

    &__title {
      font-size: $h5-font-size;

      small {
        font-size: 0.8rem;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    flex-direction: column;
    margin-top: 0;
    margin-bottom: 0;

    li {
      width: 100%;
      display: flex;
      align-items: center;
      border-bottom: 1px solid $black;

      &::after {
        content: "";
        display: block;
        background-image: url(../images/icon-arrow.svg);
        background-repeat: no-repeat;
        width: 24px;
        height: 14px;
        flex: none;
      }
    }

    a {
      flex: 1;
      padding: 1rem 1rem 1rem 0;
    }
  }

  @include media-breakpoint-down(xs) {
    &__title {
      font-size: 1rem;

      small {
        margin-top: 0.25rem;
        font-size: 0.7rem;
      }
    }

    &__icon {
      width: 30px;
      height: 30px;
    }

    a {
      padding: 0.5rem 0.5rem 0.5rem 0;
    }
  }
}

