.page-template-template-dental-exam {
  h3 {
    font-weight: 600;
    letter-spacing: 1px;
  }
}

.video-section {
  img {
    height: 100%;
  }
}

.phone-number {
  margin-top: 15px;
  font-size: 0.9rem;

  span {
    display: flex;

    a {
      p {
        color: $brand-primary;
      }
    }
  }
}

.tis-heading {
  text-align: center;
  padding-top: 90px;
  padding-bottom: 75px;

  @include media-breakpoint-down(xs) {
    padding-top: 20px;
    padding-bottom: 30px;
  }

  .tis-ht {
    font-size: 2rem;
    color: $textblack;
    line-height: 1.2;
    font-family: $playfair;
    font-weight: 600;

    @include media-breakpoint-down(sm) {
      font-size: 1.4rem;
    }
  }
}

.two-image-section {
  .row {
    margin-left: 0;
    margin-right: 0;
  }

  [class*="col-"] {
    padding-left: 0;
    padding-right: 0;
  }

  .image-position-right {
    order: 4;

    @include media-breakpoint-down(xs) {
      order: inherit;
    }
  }

  .image-position-left {
    @include media-breakpoint-down(xs) {
      margin-bottom: 15px;
    }
  }

  .tis-content-center {
    height: 100%;
    display: flex;
    justify-content: center;
    padding: 60px 100px;
    flex-direction: column;

    h3 {
      font-weight: 600;
      letter-spacing: 1px;
      margin-bottom: 18px;
    }

    @include media-breakpoint-down(sm) {
      padding: 15px;
      margin-bottom: 20px;
    }

    .btn {
      width: fit-content;
    }
  }

  img {
    width: 100%;
  }
}

.number-circle {
  border: 1px solid #bc8452;
  padding: 4px;
  border-radius: 50%;
  display: flex;
  width: 40px;
  height: 40px;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  font-family: $playfair;
  line-height: 1;
  padding-bottom: 10px;
  font-weight: 600;
  font-size: 1.2rem;
}

.insuite {
  background-color: #fff;
  padding-bottom: 100px;

  @include media-breakpoint-down(sm) {
    padding-bottom: 40px;
  }

  .insuite-upper {
    padding-top: 60px;
    padding-bottom: 44px;

    @include media-breakpoint-down(sm) {
      padding-top: 30px;
      padding-bottom: 20px;
    }
  }

  .insuite-lower {
    margin-bottom: 60px;

    h3 {
      color: $textblack;
      //font-weight: 500;
    }
  }

  .insuite-circle {
    display: flex;
    justify-content: center;
  }
}

.full-width-section-link {
  text-align: center;
}

.dental-booknow-section {
  padding-top: 100px;
  padding-bottom: 84px;
  text-align: center;
  background-color: #fff;

  @include media-breakpoint-down(sm) {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  h4 {
    color: $textblack;
    font-size: 2rem;
    font-weight: 400;
    margin-bottom: 15px;
  }

  .dbs-btn {
    margin-bottom: 15px;
  }
}



