.page-header {
  display: flex;
  width: 100%;
  position: relative;
  height: 45vh;
  max-height: 550px;
  margin-bottom: 2rem;
  background-color: $light;
  color: $body-color;
  background-size: cover;
  background-position: center;

  @include media-breakpoint-down(md) {
    max-height: 450px;
  }

  @include media-breakpoint-down(sm) {
    max-height: 250px;
  }

  &__content {
    align-self: center;
    margin: 1rem auto;
    display: block;
    position: relative;
    z-index: $zindex-page-header-content;
    padding: 0.5rem ($grid-gutter-width / 2);
    max-width: 100%;
    width: 100%;
    text-align: center;

    @include media-breakpoint-down(md) {
      margin: 2.5rem auto;
    }
  }

  &__title {
    font-size: 4rem;
    margin-bottom: 0;
    color: $white;

    @include media-breakpoint-down(md) {
      font-size: 3rem;
    }

    @include media-breakpoint-down(sm) {
      font-size: 2rem;
    }
  }

  &__dim {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: $black;
    opacity: 0.15;
  }
}



