/* stylelint-disable indentation, no-descending-specificity */

/* Booking progress bar */
.hd-progress {
    display: none;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 30px;

    .hd-progress-step-done,
    .hd-progress-step-current,
    .hd-progress-step {
        flex-grow: 1;
        flex-shrink: 1;
        flex-basis: 0%;
    }

    .hd-progress-item {
        height: 110px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        div {
            &:first-child {
                width: 60px;
                height: 60px;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 24px;
                font-weight: bold;
                font-family: $headings-font-family;
            }

            &:last-child {
                margin-top: 14px;
                font-size: $font-size-base * 0.7;
                font-family: $font-family-serif;
                white-space: nowrap;
            }
        }
    }

    .hd-progress-line {
        position: relative;
        height: 2px;
        left: 50%;
        top: -70px;
        z-index: -1;
    }

    .hd-progress-step-done {
        .hd-progress-item {
            div:first-child {
                color: $white;
                background-color: $white;
                background-image: url($image-path + 'progress-done.svg');
            }
        }

        .hd-progress-line {
            background-color: $green;
        }
    }

    .hd-progress-step-current {
        .hd-progress-item {
            div {
                &:first-child {
                    width: 74px;
                    height: 74px;
                    color: $white;
                    background-image: url($image-path + 'progress-current.svg');
                }

                &:last-child {
                    margin-top: 10px;
                }
            }
        }

        .hd-progress-line {
            background-color: $gray;
        }
    }

    .hd-progress-step {
        .hd-progress-item {
            div:first-child {
                color: $gray;
                background-image: url($image-path + 'progress-undone.svg');
            }
        }

        .hd-progress-line {
            background-color: $gray;
        }
    }
}

// @media (max-width: 992px)
@include media-breakpoint-down(sm) {
    .hd-progress {
        .hd-progress-step-current,
        .hd-progress-step,
        .hd-progress-step-done {
            .hd-progress-item {
                div {
                    &:first-child {
                        width: 50px;
                        height: 50px;
                        background-size: 50px;
                        font-size: 20px;
                    }
                }
            }
        }
    }
}

// @media (max-width: 768px)
@include media-breakpoint-down(xs) {
    .hd-progress {
        .hd-progress-step-current,
        .hd-progress-step,
        .hd-progress-step-done {
            .hd-progress-item {
                div {
                    &:last-child {
                        display: none;
                    }
                }
            }
        }

        .hd-progress-line {
            top: -55px;
        }
    }
}

// @media (max-width: NNNpx)
@include media-breakpoint-down(xxs) {
    .hd-progress {
        .hd-progress-step-current,
        .hd-progress-step,
        .hd-progress-step-done {
            .hd-progress-item {
                div {
                    &:first-child {
                        width: 40px;
                        height: 40px;
                        background-size: 40px;
                        font-size: 20px;
                    }
                }
            }
        }

        .hd-progress-line {
            top: -55px;
        }
    }
}



