.btn {
  font-family: $font-family-sans-serif;
  border-radius: 10rem;
  text-transform: uppercase;
  //font-size: 0.8rem;

  &-primary {
    color: $white;

    &:hover {
      background-color: $secondary;
    }
  }

  &-white {
    &:hover {
      color: $white;
      background-color: $secondary;
    }
  }

  &-sm {
    font-size: 0.7rem;
  }

  &-lg {
    font-size: 0.8rem;
  }
}

.btn-sub-nav {
  background-color: $light;
  text-align: left;
  white-space: inherit;
  padding: 1rem;
  border-radius: 0;

  &__text {
    float: left;
    font-size: 0.9rem;
    width: calc(100% - 30px);
  }

  &__icon {
    float: right;
    display: block;
    height: 23px;
    width: 25px;
    background-image: url(../images/menu-burger.svg);
    background-size: cover;
    margin-top: 1px;
  }

  &:focus {
    box-shadow: none;
  }
}

.btn-link {
  text-transform: uppercase;
  font-size: 0.7rem;
  padding-left: 0;
  padding-right: 0;
  font-weight: bold;

  &:hover {
    text-decoration: none;
  }

  &::before,
  &::after {
    display: inline-block;
    background-image: url(../images/icon-arrow.svg);
    background-repeat: no-repeat;
    width: 16px;
    height: 11px;
  }

  &::after {
    content: "";
    margin-left: 0.5rem;
  }

  &::before {
    margin-right: 0.5rem;
    transform: scaleX(-1);
  }
}



