.dropdown {
  position: absolute;
  top: 0;
  width: 100%;

  &:focus {
    outline: none;
  }
}

.dropdown-container {
  margin: 0;
  padding: 0;
  background: #fcf7f5;
  position: relative;
  z-index: 99;

  &:focus {
    outline: none;
  }

  .sub-menu {
    margin: 0;
    padding-bottom: 5px;
    display: none;
  }
}

.booking-menu {
  &:focus {
    outline: none;
  }

  .sub-menu {
    &:focus {
      outline: none;
    }
  }

  display: block;

  a {
    color: #000;
    text-decoration: none;
  }

  > .js-menu-accordion {
    display: block;
    position: relative;
    cursor: pointer;
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;

    > li {
      list-style: none;
      margin: 0;
      padding: 0;

      > a {
        font-size: 16px;
        display: block;
        color: #000;
        padding-left: 20px;
        padding-bottom: 9px;
        outline: 0;

        &:hover {
          color: #f93333;
          text-decoration: none;
        }

        &:focus {
          color: #f93333;
          text-decoration: none;
        }

        &:active {
          color: #f93333;
          text-decoration: none;
        }
      }
    }
  }

  .dropdown-title {
    font-size: 13px;
    position: relative;
    width: calc(100% - 45px);
    display: block;
    height: auto;
    line-height: 1.1;
    padding-bottom: 15px;
    padding-left: 20px;
    padding-top: 16px;
    padding-right: 10px;
    text-decoration: none;
    outline: 0;
    color: #000;
    text-transform: uppercase;
  }

  .dropdown-btn {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    height: 45px;
    width: 45px;
    position: absolute;
    right: 0;
    top: 0;
    transition: -webkit-transform 0.2s cubic-bezier(0.77, 0, 0.175, 1);
    cursor: pointer;
  }

  .collapse-icon {
    position: relative;
    width: 26px;
    height: 26px;

    &::before {
      content: "";
      position: absolute;
      background-image: url(/wp-content/uploads/2020/09/black-arrow-drop.svg);
      transition: transform 120ms cubic-bezier(0.4, 0, 0.4, 1);
      background-repeat: no-repeat;
      width: 23px;
      height: 13px;
      top: 8px;
      left: 0;
    }

    &:hover {
      cursor: pointer;
    }
  }
}

.booking-menu.active {
  .sub-menu {
    display: block;
    box-shadow: 7px 6px 7px #00000024;
  }
}

.collapse-icon.rotate.down {
  &::before {
    transform: rotate(180deg);
  }
}

@media (max-width: 767.98px) {
  .dropdown-container {
    background: #fff;

    .sub-menu {
      padding-top: 3px;
    }
  }

  .booking-menu {
    .collapse-icon {
      width: 16px;
      height: 16px;

      &::before {
        width: 20px;
        height: 10px;
      }
    }

    .dropdown-title {
      width: calc(100% - 38px);
      padding-bottom: 11px;
      padding-top: 13px;
    }

    .dropdown-btn {
      height: 38px;
      width: 38px;
    }
  }

  .home {
    .dropdown-container {
      background: #eee;
    }
  }
}



