/* stylelint-disable indentation, no-descending-specificity */

/* Booking step */
.hd-step {
    display: none;
    height: calc(100vh - 136px);
    margin-bottom: 60px;

    @media (max-width: 767px) {
        height: auto;
    }

    h1 {
        border-bottom: 4px solid #fa7864;
        padding-bottom: 12px;
        font-size: 2.5rem;

        @include media-breakpoint-down(xs) {
            font-size: 1.5rem;
        }
    }
}

.hd-step-left-part {
    overflow: auto;
    height: calc(100vh - 136px);
    padding-top: 30px;
    padding-bottom: 30px;
    padding-right: 60px;

    .hd-back-button {
        margin-bottom: 10px;

        .hd-step-footer-prev {
            margin: 0 0;
            color: #fa7864;
            text-transform: uppercase;
            font-size: 0.7rem;
            font-weight: bold;
            font-family: $roboto;
            display: flex;
            align-items: center;
            line-height: 1;
        }
    }

    @media (max-width: 767px) {
        padding-right: 15px;
        height: auto;
    }
}

.hd-step-right-part {
    overflow: hidden;
    height: calc(100vh - 136px);

    #hd-booking-clinics-map {
        height: 100%;
        width: 100%;
        display: none;
        position: relative;
    }

    @media (max-width: 767px) {
        height: 50vh;
    }
}

.hd-step-content {
    a {
        text-decoration: none;
    }

    label {
        font-size: 14px;
        margin-bottom: 3px;
    }

    .form-control {
        margin: 0;
        width: 100%;
        border: 0;
        border-bottom: 1px solid #c9c9c0;
        padding-left: 0;

        &:focus {
            box-shadow: none;
        }
    }

    .hd-time-row {
        padding-left: 0;
        border-bottom: 2px solid #c9c9c0;
        margin-bottom: 15px;

        .hd-time-wrapper {
            margin-bottom: 15px;

            .hd-time-date {
                color: #6c675c;
                font-size: 18px;
                font-weight: 700;
                margin-bottom: 10px;
            }

            .hd-time-slots {
                display: flex;
                flex-wrap: wrap;

                .hd-selecttimeslot {
                    background-color: #efede8;
                    border-radius: 30px;
                    font-size: 14px;
                    color: #6c675c;
                    display: flex;
                    margin-right: 15px;
                    margin-bottom: 5px;
                    flex-wrap: wrap;
                    padding: 6px 17px;
                    cursor: pointer;
                }

                .hd-time-error {
                    font-size: 14px;
                    color: #6c675c;
                }
            }
        }
    }

    .hd-week-selection {
        display: flex;
        justify-content: space-between;
        border-bottom: 4px solid #fa7864;
        padding-bottom: 10px;
        margin-bottom: 15px;

        .hd-week-text {
            font-size: 16px;
            text-transform: uppercase;
            font-weight: 700;
        }

        .fa {
            cursor: pointer;
        }

        .fa-chevron-right {
            color: #5b574e;
        }
    }

    .hd-btn-area {
        margin-top: 10px;
    }
}

/* Step 2 - Select treatment */
.hd-treatment-row {
    //margin: 0 0 10px 0;
    padding: 20px 0 20px 0;
    //background-color: $brand-primary-light;
    align-items: center;
    border-bottom: 2px solid #c9c9c0;
    cursor: pointer;

    h5 {
        margin: 0 0 5px;
        font-size: 1.5rem;
        line-height: 1.3;
        font-weight: 700;
        color: #6c675c;
        font-family: $playfair;
    }

    p {
        margin: 0;
        font-size: 0.9rem;
        line-height: 1.2;
        color: #6c675c;
    }

    .hd-col-btn a {
        float: right;
    }

    .hd-treatment-content {
        display: flex;
        align-items: center;

        .hd-treatment-text {
            flex: 1;
        }

        .hd-treatment-icon {
            .fa {
                color: rgba(108, 103, 92, 0.54);
            }
        }
    }

    &:hover {
        .hd-treatment-icon {
            .fa {
                color: #5b574e;
            }
        }
    }
}

/* Step 4 - Select time and care giver */
.hd-filter-row {
    align-items: center;
    margin-bottom: 20px;
    margin-top: 20px;

    label {
        font-size: 14px;
        margin-bottom: 3px;
    }

    .form-control {
        margin: 0;
        width: 100%;
        border: 0;
        border-bottom: 1px solid #c9c9c0;
        padding-left: 0;
        background-color: transparent;

        &:focus {
            box-shadow: none;
        }
    }
}

#hd-step-4-more {
    width: 100%;
    margin-top: 10px;
}

/* Step 5 - Personal information */
#hd-step-5-personal {
    display: none;
}

#hd-step-5-logon {
    display: none;

    #hd-step-5-logon-ssn {
        width: 200px;
    }

    #hd-step-5-logon-login {
        width: 200px;
        margin-top: 20px;
    }

    .hd-description {
        margin-top: 15px;
        margin-bottom: 30px;
    }
}

#hd-step-5-extra {
    margin-top: 30px;
}

#hd-step-5 {
    .or-gap {
        margin-top: 20px;
        margin-bottom: 20px;
        color: #fa7864;
    }

    .hd-step-content {
        h4 {
            color: #6c675c;
            font-size: 22px;
            font-weight: 700;
            font-family: $playfair;
        }
    }

    .hd-description {
        margin-bottom: 20px;
    }

    .hd-5-check-area {
        margin-top: 20px;
        margin-bottom: 15px;
    }

    #discount-discription {
        font-size: 12px;
        color: #6c675c;
    }
}

