/* stylelint-disable indentation, no-descending-specificity */

.hd-clinic-phone img {
    margin-right: 5px;
    height: 20px;
}

.hd-clinic-row {
    margin: 0;
    padding: 12px 0 10px 20px;
    border-bottom: 1px solid #ebd9d1;

    &:first-child {
        padding-top: 0;
    }
}

.hd-clinic-col {
    padding: 5px 0 0 0;
    font-size: $font-size-base * 0.8;
    font-family: $font-family-serif;
    color: $dark;

    img {
        margin-right: 5px;
    }
}

.hd-clinic-col-btn {
    padding-top: 2px;

    a {
        float: right;
    }
}

.hd-clinic-col-text {
    padding-top: 8px;
}

.hd-clinic-col h4,
.hd-clinic-distance,
.hd-clinic-phone {
    white-space: nowrap;
}

.hd-clinic-phone a,
.hd-clinic-col h4 a {
    color: $dark;
}

.hd-clinic-distance {
    color: $brand-primary;
}

/* Clinic details */
.hd-clinic-details > .row > div > .row {
    padding-bottom: 15px;
}

.hd-clinic-details-text {
    font-size: $font-size-base * 0.8;
}

.hd-clinic-details-open {
    font-size: $font-size-base * 0.8;

    div {
        white-space: nowrap;
    }
}

.hd-clinic-details-header {
    margin-top: 10px;
    font-size: $font-size-base * 0.8;
    text-transform: uppercase;
}

.hd-clinic-details-icon img {
    margin-top: 3px;
    max-width: initial !important;
    height: 22px !important;
}

.hd-clinic-details-distance {
    color: $brand-primary;
}

.hd-clinic-details-btn a {
    margin-top: 10px;
    margin-right: 10px;
}

/* Extending base theme */
.clinic-accordion .card {
    border: none;

    .card-header {
        background-color: $brand-primary-light;
        padding: 20px;
        margin-bottom: 10px;
    }
}

/* --------------------------------------------------------------

# Text mods

-------------------------------------------------------------- */

.content-details > p,
.content-details > h2 {
  display: none;
}

.content-details.open > p,
.content-details.open > h2 {
  display: block;
}

.content-details .content-readmore {
  box-sizing: border-box;
  direction: ltr;
  position: absolute;
  width: 100%;
  height: 145px;
  bottom: 0;
  background: linear-gradient(rgba(255, 255, 255, 0) 15%, rgb(255, 255, 255) 75%);
}

.content-details.open > .content-summary::after {
  background: transparent;
}

.content-details.open .content-readmore {
  position: static;
  height: auto;
}

.content-details > .content-summary {
  position: relative;
  margin-bottom: 2em;
  max-height: 152px;
  overflow: hidden;
  direction: ltr;
}

.content-details.open > .content-summary {
  max-height: inherit;
  overflow: visible;
}

.content-details .button_details {
  cursor: pointer;
  display: inline-block !important;
  box-sizing: border-box;
  direction: ltr;
  position: relative;
  bottom: -102px;
}

.content-details.open .content-readmore .button_details {
  bottom: 0;
}

@media (min-width: 768px) {
  .content-details > .content-summary {
    max-height: 185px;
  }

  .content-details .content-readmore {
    height: 200px;
    background: linear-gradient(rgba(255, 255, 255, 0) 30%, rgb(255, 255, 255) 75%);
  }

  .content-details .button_details {
    bottom: -155px;
  }
}



