.di-service {
  padding-top: 60px;
  padding-bottom: 30px;

  h4 {
    font-weight: 600;
    margin-bottom: 15px;
  }
}

.review-carousel {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: #efede8;
  margin-top: 60px;
  text-align: center;

  .carousel-inner {
    padding: 0 80px;

    @include media-breakpoint-down(xs) {
      padding: 0 40px;
    }
  }

  .carousel-control-prev {
    background-color: transparent;
  }

  .carousel-control-next {
    background-color: transparent;
  }
}

.image-slider {
  padding-top: 60px;

  .carousel-section {
    h2 {
      margin-top: 30px;
    }

    .owl-carousel {
      margin-top: 35px;
    }
  }
}

.easy-step {
  padding-top: 30px;
  padding-bottom: 60px;

  .easy-heading {
    margin-bottom: 70px;
    text-align: center;
  }

  .number-circle {
    margin-top: 22px;
    margin-bottom: 22px;
  }

  h4 {
    font-weight: 600;
    letter-spacing: 1px;
  }
}

.learn-more-di {
  padding-top: 60px;
  padding-bottom: 45px;
  background-color: #fff;

  @include media-breakpoint-down(sm) {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .lmdi-img {
    margin-bottom: 30px;

    img {
      width: 100%;
    }
  }

  hr {
    margin-top: 0.6rem;
    margin-bottom: 0.6rem;
  }
}

.video-section-di {
  padding-top: 50px;

  @include media-breakpoint-down(sm) {
    padding-top: 20px;
  }

  .video-wrapper {
    position: relative;
    margin-top: 1.7rem;

    a {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      width: 80px;
      height: 80px;
      margin: auto;
      background: #fff;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      text-decoration: none;
    }
  }
}

.di-btn-area {
  .btn-and-phone {
    padding-bottom: 40px;
  }
}



