.hq-cta-ib {
  background-size: cover;
  background-position: center;
  color: #fff;
  padding: 0 5rem;
  max-height: 440px;

  .row {
    padding: 5rem 1rem;
  }
}

.cta-ib-logo {
  text-align: right;

  img {
    width: 65%;
    margin-top: -2rem;
  }
}

section.hq-cta-ib {
  div {
    div {
      padding: 0;
    }
  }
}

.cta-ib-bullets {
  ul {
    padding-left: 0;

    li {
      list-style: none;
      display: flex;
      align-items: baseline;
      line-height: 1.8;

      &::before {
        content: "";
        background-image: url(/wp-content/uploads/2020/09/white-arrow-v1.svg);
        background-repeat: no-repeat;
        background-position: bottom;
        width: 23px;
        height: 13px;
        -webkit-box-flex: 0;
        -ms-flex: none;
        flex: none;
      }
    }
  }

  span {
    line-height: 1.7;
  }
}

#cta-ib-button {
  background: #fff;
  color: #333;

  &:hover {
    background: #f93333;
    color: #fff;
  }
}

#cta-ib-logo-2 {
  display: none;
}

@media (max-width: 1599.98px) {
  section.hq-cta-ib {
    padding: 0 2rem;
  }
}

@media (max-width: 767px) {
  #cta-ib-logo-1 {
    display: none;
  }

  #cta-ib-logo-2 {
    display: block;
    text-align: left;
    margin-bottom: 1rem;
  }

  .hq-cta-ib {
    .row {
      padding: 2rem 1rem;
    }
  }

  .cta-ib-bullets {
    h3 {
      display: none;
    }
  }

  .cta-ib-logo {
    img {
      width: 100%;
      max-width: 300px;
      margin-top: 1rem;
    }
  }
}



