.p-r {
  height: auto;

  &::before {
    content: '';
    position: absolute;
    width: 140%;
    height: 100%;
    left: 0;
    bottom: 0;
    background: var(--url-image-var);
    background-size: cover;
  }
}

.two-col-html-image {
  overflow: hidden;
}

.html-content-col {
  padding: 5rem 3rem 5rem 0;
}

@media (max-width: 992px) {
  .p-r {
    display: none;
  }

  .html-content-col {
    padding: 5rem 0;
  }
}

@media (max-width: 622px) {
  .two-col-html-image {
    .container {
      padding: 0 40px !important;
    }
  }
}



