//news detail page scss start here
.news-detail-image {
  margin-top: 15px;
  margin-bottom: 30px;
  //max-width: 500px;
}

.news-time {
  font-size: 0.8rem;
  font-weight: 500;
}

.news-detail-content {
  padding-bottom: 60px;
}

//new page scss starts here

.blog-content-wrapper {
  padding-bottom: 60px;

  .bottom-gap {
    margin-bottom: 80px;
  }

  .blog-container {
    .images-container {
      .blog-main-image {
        height: 215px;
        overflow: hidden;
        width: 100%;
        margin-bottom: 20px;
        background-position: top;
        background-size: cover;

        img {
          width: 100%;
          object-fit: cover;
          object-position: center;
        }
      }

      .blog-logo-image {
        height: 50px;
        width: 100%;
        overflow: hidden;
        margin-bottom: 20px;
      }
    }

    .blog-content {
      margin-bottom: 10px;
    }
  }
}



