/* stylelint-disable indentation, no-descending-specificity */

a.btn-js {
    color: $white !important;
}

.hd-btn-secondary {
    margin: 0 20px;
    color: $brand-primary;
    text-transform: uppercase;
    font-size: $font-size-base * 0.7;
    font-family: $font-family-sans-serif;
    font-weight: bold;
}

/* Icon in input */
.hd-inner-addon {
    display: inline-block;
    position: relative;
    width: 100%;

    .hd-inner-addon-icon {
        position: absolute;
        margin: 12px;
        width: 18px;
        height: 18px;
        pointer-events: none;
    }
}

.hd-left-addon {
    .hd-inner-addon-icon {
        left: 0;
    }

    input {
        padding-left: 35px;
        height: calc(2.25rem + 2px);
    }
}

.hd-right-addon {
    .hd-inner-addon-icon {
        right: 0;
    }

    input {
        padding-right: 35px;
        height: calc(2.25rem + 2px);
    }
}



