.booking-header {
  padding: 15px 0;
  border-bottom: 1px solid #c9c9c0;
}

.grid-container {
  display: grid;
  align-items: center;

  .fa {
    cursor: pointer;
  }
}

.item1 {
  grid-column: 1;
  grid-row: 1;
}

.item2 {
  grid-column: 2;
  grid-row: 1;
  padding-left: 39px;

  @include media-breakpoint-down(xs) {
    padding-left: 0;
  }
}

.page-template-template-clinic-booking {
  .hd-step {
    height: calc(100vh - 81px);

    @include media-breakpoint-down(xs) {
      height: auto;
    }

    .hd-step-left-part {
      height: calc(100vh - 81px);

      @include media-breakpoint-down(xs) {
        height: auto;
      }
    }

    .hd-step-right-part {
      padding-left: 0;
      height: calc(100vh - 81px);

      @include media-breakpoint-down(xs) {
        height: 50vh;
        padding-left: 15px;
      }
    }
  }
}

.hd-step-6 {
  padding-bottom: 30px;
  display: none;

  .hd-step-content {
    margin-bottom: 30px;
  }
}

.tooltip-text {
  padding: 20px;

  p {
    font-size: 0.6rem;
    text-transform: uppercase;
    font-weight: 500;
    font-family: $roboto;
    margin-bottom: 5px;
    color: $textblack;
  }

  h4 {
    margin-bottom: 0;
    font-weight: 700;
  }

  hr {
    margin-top: 1rem;
    margin-bottom: 1rem;
  }

  .small {
    font-size: 0.7rem;
    color: $textblack;
    font-weight: 400;
    margin-bottom: 10px;
  }
}



