/* stylelint-disable indentation, no-descending-specificity */

.hd-mandatory {
    color: $brand-primary-dark;
    margin-left: 8px;
}

.hd-mandatory-info {
    color: $brand-primary-dark;
    font-size: $font-size-base * 0.8;
}

.hd-error,
.hd-info {
    margin: 20px 0;
    padding: 15px;
    display: none;
}

.hd-info {
    background-color: $green;
    color: $white;

    .hd-clinicname {
        color: $white;
    }
}

.hd-error {
    //background-color: $brand-primary-dark;
    color: $brand-primary-dark;
    padding-left: 0;
    margin-top: 0;
    padding-top: 2px;
    font-size: 16px;

    span,
    a {
        color: $white !important;
    }

    a {
        text-decoration: underline;
    }
}

.hd-error-inverted,
.hd-error-transparent {
    background-color: transparent;
    color: $brand-primary-dark;

    .hd-clinicname {
        color: $brand-primary-dark;
    }
}

.hd-error-inverted {
    background-color: $white;
}

.hd-show {
    display: block;
}

.hd-hidden {
    display: none;
}



