.blockquote {
  font-family: $headings-font-family;
  font-size: inherit;
  quotes: '\201c' '\201d';
  max-width: 1200px;
  margin: 0 auto;

  p {
    font-weight: $headings-font-weight;
    font-size: 2rem;
    margin-bottom: 0;
    line-height: 1.3;
    text-align: center;
    font-style: italic;

    &::before {
      content: open-quote;
    }

    &::after {
      content: close-quote;
    }

    @include media-breakpoint-down(lg) {
      font-size: 2rem;
    }

    @include media-breakpoint-down(sm) {
      font-size: 1.5rem;
    }
  }

  footer {
    margin-top: 0.5rem;
    width: 100%;
    text-align: center;
    font-size: inherit;

    @include media-breakpoint-down(sm) {
      font-size: 0.8rem;
    }
  }

  cite {
    font-style: normal;
  }
}



