.ib-easy-steps {
  padding-top: 90px;
  padding-bottom: 90px;

  .ib-heading {
    text-align: center;
    margin-bottom: 40px;
  }

  .ib-easy-group {
    display: flex;
    margin-top: 20px;

    .number-circle {
      margin-right: 10px;
      flex: none;
      text-align: center;
    }
  }
}

.ib-group-line {
  margin-bottom: 5px;
  background: url('../../images/arrow-both-side.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 0 12px;

  span {
    color: $brand-primary-dark;
    font-size: 0.8rem;
    display: inline-block;
    padding: 10px 30px;
    background-color: #f9f9f5;
    position: relative;
    top: -6px;
  }
}

.four-reason-section {
  padding-top: 90px;
}

.ib-option-section {
  padding-top: 90px;

  .black-heading {
    text-align: center;
    margin-bottom: 90px;
  }

  .ib-option-image {
    margin-bottom: 25px;

    img {
      width: 100%;
    }
  }

  h3 {
    font-weight: 700;
    color: $textblack;
    margin-bottom: 2px;
  }

  .price-comparison__price-label {
    font-size: 0.7rem;
    margin-bottom: 10px;
  }

  .price-comparison__price-note {
    font-size: 0.7rem;

    span {
      font-size: 1rem;
    }
  }
}

.ib-guidance-section {
  padding-top: 60px;

  .beige-bg {
    background-color: $beige;

    @include media-breakpoint-down(sm) {
      background-color: transparent;

      .inside-padding {
        background-color: $beige;
      }
    }
  }

  ul {
    li {
      list-style: none;
      position: relative;
      margin-bottom: 20px;

      &::before {
        content: "";
        background: url('../../images/listing-icon.png');
        width: 23px;
        height: 23px;
        display: inline-block;
        position: absolute;
        left: -38px;
        top: 4px;
      }
    }
  }

  .row {
    margin-left: 0;
    margin-right: 0;
  }
}

.ib-package-section {
  margin-top: 90px;

  .ib-grey-bg {
    background-color: #efede8;
    padding: 30px;

    @include media-breakpoint-down(sm) {
      padding: 30px 15px;
    }

    .free-text {
      color: $brand-primary-dark;
      font-size: 0.8rem;
    }

    .value-sek {
      font-size: 0.8rem;
    }

    .ib-package-image {
      margin-top: 30px;
    }
  }
}

.smart-fast {
  .grey-bg {
    background-color: $grey-dark;

    @include media-breakpoint-down(sm) {
      background-color: transparent;

      .inside-padding {
        background-color: $grey-dark;
      }
    }
  }

  .row {
    margin-right: 0;
  }
}

.inside-padding {
  padding: 60px;

  h2 {
    margin-bottom: 30px;
  }

  @include media-breakpoint-down(xs) {
    padding: 15px;
  }
}

.ib-tab-section {
  padding-bottom: 60px;
  padding-top: 60px;
  background-color: #fff;

  @include media-breakpoint-down(xs) {
    padding-bottom: 30px;
    padding-top: 30px;
  }

  h2 {
    text-align: center;
  }
}

.ib-form-section {
  .second-part {
    flex-direction: column;
  }
}

.banner-rating {
  .treatments-hero__rating-value {
    font-weight: 600;
  }

  p {
    margin-bottom: 2px;
    font-size: 0.8rem;
  }
}

.stars-container {
  .fas {
    margin-right: 6px;

    &:last-child {
      margin-right: 0;
    }
  }
}

.ib-consult-area {
  background-color: #fff;
  padding-top: 60px;
  padding-bottom: 60px;
  text-align: center;

  .black-heading {
    p {
      font-size: 2rem;
      font-weight: 400;
    }
  }

  .call-to-action__title {
    color: $textblack;
  }
}



