.sidebar-nav {
  font-family: $font-family-sans-serif;
  font-size: 1rem;
  padding-right: 1rem;

  a {
    display: block;
    text-decoration: none;
    line-height: 1.5;
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
  }

  & > ul > li > a {
    font-size: 1.2rem;
    font-weight: normal;
  }

  // .current_page_parent > a,
  .active > a {
    font-weight: bold;
    color: $body-color;

    &::before {
      content: "";
      display: inline-block;
      width: 7px;
      height: 14px;
      margin-right: 0.25rem;
      background-image: url(../images/icon-active-link.svg);
      background-size: cover;
    }
  }

  .children {
    margin-left: 1rem;

    .page_item a {
      color: rgba($body-color, 0.75);

      &:hover {
        color: $body-color;
      }
    }

    .page_item.active a {
      color: $body-color;
    }
  }

  @include media-breakpoint-down(md) {
    border-top: 1px solid $white;
    background-color: $light;
    padding: 1rem;
  }
}



