.treatment-image-text-wrapper {
  .t-image-wrapper {
    //margin-bottom: 15px;
  }

  .right-content {
    padding-left: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;

    @include media-breakpoint-down(xs) {
      padding-left: 0;
      height: auto;
    }

    hr {
      margin-top: 0;
      border-color: #fa7864;
    }

    h2 {
      font-weight: 700;
      font-size: 2rem;
      color: #6c675c;
      line-height: 1.3;
      font-family: $playfair;
      margin-bottom: 15px;
    }

    ul {
      padding-left: 0;
      border-bottom: 4px solid #c9c9c0;
      border-top: 1px solid #c9c9c0;
      margin-top: 60px;

      li {
        list-style: none;
        padding-bottom: 10px;
        padding-top: 10px;
        border-bottom: 1px solid #c9c9c0;
        font-size: 1rem;

        @media (min-width: 1439px) {
          font-size: 1.3rem;
        }

        &:last-child {
          border-bottom: 0;
        }
      }
    }

    @media (max-width: 767px) {
      margin-top: 20px;
    }
  }
}

.treat-text-section {
  padding-bottom: 50px;
  padding-top: 50px;

  @include media-breakpoint-down(xs) {
    padding-top: 20px;
  }

  .treatment-grid-section {
    margin-bottom: 15px;

    h3 {
      color: $textblack;
      font-weight: 600;
    }
  }

  .veneer-craft-text {
    @media (min-width: 991px) {
      background: #efede8;
    }

    .veneer-craft-inner-bg {
      background: #efede8;
      padding: 30px 60px;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;

      hr {
        margin-top: 0.4rem;
      }

      @include media-breakpoint-down(sm) {
        padding: 15 30px;
      }
    }

    h2 {
      color: $textblack;
      //font-size: 1.4rem;
      font-weight: 700;
      margin-bottom: 30px;
      letter-spacing: 1px;
    }

    h3 {
      color: $textblack;
      //font-size: 1.2rem;
      border-top: 1px solid #c9c9c0;
      padding-top: 10px;
      font-weight: 500;
    }
  }
}

.team-wrapper {
  .team-grid {
    margin-bottom: 15px;

    .team-image {
      max-height: 470px;
      overflow: hidden;

      img {
        object-fit: cover;
        width: 100%;
      }
    }

    h3 {
      color: $textblack;
      font-weight: 600;
      margin-bottom: 20px;
    }
  }
}

.easy-way-payment {
  .easy-way-grid {
    &.black-text {
      .plan-price {
        h3 {
          color: $textblack;
        }
      }
    }

    .plan-items {
      &.last-item {
        .small {
          font-size: 12px;
          margin-bottom: 3px;
        }

        .plan-price {
          h3 {
            font-weight: 700;
            font-family: $playfair;

            .sek {
              font-size: 1.2rem;
            }

            .sek-price {
              font-size: 2.5rem;
              margin-left: 4px;
            }

            .sek-month {
              font-size: 1.1rem;
            }
          }

          p {
            font-size: 0.8rem;
          }
        }
      }
    }
  }
}

.venner-smile-grey-bg {
  margin-bottom: 10px;

  .row {
    margin-left: 0;
    margin-right: 0;
    background: #efede8;
    padding-left: 45px;
    padding-right: 45px;

    @include media-breakpoint-down(xs) {
      padding-left: 0;
      padding-right: 0;
      padding-top: 30px;
      padding-bottom: 10px;
    }
  }

  [class*="col"] {
    //background-color: #efede8;
    padding-top: 30px;
    padding-bottom: 14px;

    @include media-breakpoint-down(xs) {
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }

  .main-title {
    color: $textblack;
  }

  .sub-title {
    font-size: 0.8rem;
  }
}

.veener-treatment-plan {
  .vtp-bg {
    background-color: #efede8;
    height: 100%;
    padding: 60px;
    font-family: $playfair;
    display: flex;
    flex-direction: column;
    justify-content: center;

    @include media-breakpoint-down(sm) {
      padding: 15px;
    }
  }

  .first-div {
    padding-right: 5px;

    @include media-breakpoint-down(xs) {
      padding-right: 15px;
      margin-bottom: 10px;
    }
  }

  .second-div {
    padding-left: 5px;

    @include media-breakpoint-down(xs) {
      padding-left: 15px;
    }
  }
}

.btn-and-phone {
  text-align: center;
  padding-top: 60px;
  padding-bottom: 80px;

  @include media-breakpoint-down(sm) {
    padding-bottom: 30px;
    padding-top: 40px;
  }

  .btn {
    margin-bottom: 10px;
  }
}

.faq-treatments {
  background-color: #efede8;
  padding-top: 60px;
  padding-bottom: 60px;

  .faq-left {
    @include media-breakpoint-down(sm) {
      margin-bottom: 20px;
    }
  }
}

.accordion {
  &.accordion-flush {
    border-top: 1px solid #c9c9c0;
    padding-top: 18px;

    .accordion-item {
      border-bottom: 1px solid #c9c9c0;
      margin-bottom: 15px;
      padding-bottom: 6px;
      cursor: pointer;

      & > .accordion-header {
        font-size: 1.2rem;
        color: $textblack;
        font-weight: 500;
        display: flex;
        justify-content: space-between;

        .fas {
          color: $textblack;
        }
      }

      .accordion-collapse {
        & > .accordion-body {
          color: $textblack;
          font-size: 1rem;
        }
      }
    }
  }
}

.what-is-veneer-section {
  background-color: #efede8;
  padding-top: 60px;
  padding-bottom: 60px;
  margin-top: 50px;

  &.beige-venner {
    background-color: #f9f3ed;
  }

  .v-heading {
    text-align: center;
    //font-size: 2rem;
    //color: #bc8453;
    //margin-bottom: 10px;
    //line-height: 1.2;
    //font-family: $playfair;
  }
}

.slider-nav-tabs {
  nav {
    text-align: center;
  }

  .nav-tabs {
    justify-content: center;
    position: relative;
    display: inline-flex;
    border-bottom: 4px solid #c9c9c0;

    .nav-item {
      position: relative;

      &::after {
        content: "";
        width: 10px;
        height: 2px;
        background-color: $brand-primary;
        display: inline-block;
        position: absolute;
        bottom: 31px;
        right: -20px;

        @include media-breakpoint-down(xs) {
          content: none;
        }
      }

      &:last-child {
        &::after {
          content: none;
        }
      }
    }

    .nav-link {
      margin-right: 20px;
      position: relative;
      text-transform: capitalize;
      font-weight: 400;

      &:last-child {
        margin-right: 0;
      }

      &.active {
        background-color: transparent;
        font-weight: 600;

        &::after {
          content: "";
          width: 100%;
          height: 4px;
          display: block;
          position: absolute;
          bottom: -3px;
          z-index: 9;
          background-color: $brand-primary;
        }
      }
    }
  }
}

.expertise-right-block {
  .expertise-pop {
    .title {
      font-size: 1.3rem;
      font-weight: 600;
      color: $textblack;
      font-family: $playfair;
      margin-bottom: 10px;
    }
  }
}

.before-after-content {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

  .bef-after-img {
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
  }
}



