.clinic-accordion {
  .card {
    border-left: 0;
    border-right: 0;
    background-color: transparent;
    border-top: 0;
  }

  .card-header {
    padding-right: 0;
    padding-left: 0;
    border: none;
    background-color: transparent;
  }

  .card-header-content {
    font-size: $h3-font-size;
    padding: 0;
    margin-bottom: 0;
  }

  .card-header-btn {
    background-color: transparent;
    border: 0;
    display: flex;
    align-items: center;
    text-align: left;
    width: 100%;
    padding: 0;

    &:active {
      color: inherit;
    }

    &::after {
      content: "";
      display: block;
      width: 25px;
      height: 25px;
      background-image: url(../images/icon-minus.svg);
      background-size: cover;
      margin-left: auto;
    }

    &.collapsed {
      &::after {
        background-image: url(../images/icon-plus.svg);
      }
    }
  }

  .card-body {
    padding-top: 0;
    padding-right: 0;
    padding-left: 0;
  }
}

.clinic-card {
  $el: &;

  background-color: $light;
  display: flex;
  font-size: 0.8rem;
  margin-bottom: $grid-gutter-width;

  &__image {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    flex: none;
    flex-basis: calc(100% / 3);
  }

  &__header {
    width: 100%;
    margin-bottom: 0.5rem;

    .entry-title {
      font-size: $h5-font-size;
    }

    a {
      color: inherit;
    }

    .entry-title,
    #{$el}__location {
      margin-bottom: 0;
    }
  }

  &__contact {
    margin-bottom: 1rem;

    a {
      color: inherit;
    }

    span {
      margin-left: 0.25rem;
    }
  }

  &__content {
    padding: 1rem;
    flex: 1;

    a {
      word-break: break-all;
    }
  }

  &__buttons {
    .btn {
      &:not(:first-of-type) {
        margin-left: 0.5rem;
      }
    }

    // @include media-breakpoint-down(lg) {
    //   .btn {
    //     display: block;

    //     + .btn {
    //       margin-left: 0;
    //       margin-top: 5px;
    //     }
    //   }
    // }
  }

  @include media-breakpoint-down(lg) {
    &__header,
    &__contact {
      margin-bottom: 0.5rem;
    }

    &__buttons {
      .btn {
        &:not(:first-of-type) {
          margin-left: 0.5rem;
        }
      }
    }
  }

  @include media-breakpoint-down(xs) {
    &__image {
      display: none;
      height: 200px;
    }
  }

  @include media-breakpoint-down(md) {
    &__content {
      padding: 0.6rem;
    }
  }
}

.opening-hours {
  padding-left: 0;
  margin-bottom: 0;
  font-family: $font-family-sans-serif;

  &__day {
    display: flex;
  }

  &__day-name {
    flex: 1;
  }

  &__openhours {
    flex: none;
  }
}

.clinic-map {
  width: 100%;
  height: 200px;

  /* fixes potential theme css conflict */
  img {
    max-width: inherit !important;
  }
}

// Team page
.staff {
  margin-top: 2rem;

  &__item {
    display: flex;
    align-items: center;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
  }

  &__image {
    flex: none;

    img {
      max-width: 70px;
      height: auto;
      border-radius: 100%;
    }
  }

  &__content {
    margin-left: 1rem;
  }

  &__name {
    font-size: 1rem;
  }

  &__role {
    font-size: 0.8rem;
  }

  &__name,
  &__role {
    margin-bottom: 0;
  }

  @include media-breakpoint-down(md) {
    &__image {
      img {
        max-width: 40px;
      }
    }

    &__item {
      margin-top: 0;
    }

    &__content {
      margin-left: 0.5rem;
    }
  }
}



