.notification-bar {
  background-color: #4d4942;

  &:hover {
    background-color: #fa7864;
  }

  a {
    color: #fff;
    display: block;
    padding: 15px 15px;
    cursor: pointer;
    font-size: 0.8rem;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 1px;
  }

  #toggle-icon {
    cursor: pointer;
  }

  .content {
    display: none;
    padding-top: 15px;
    padding-bottom: 10px;

    p {
      color: #fff;
      font-size: 16px;
      margin-bottom: 2px;
    }
  }

  .chevron-link { color: #fff; }
}

/* stylelint-disable no-descending-specificity */
.site-header {
  font-family: $font-family-sans-serif;
  position: relative;
  z-index: $zindex-site-header;

  ul {
    padding-left: 0;
    list-style: none;
    margin-bottom: 0;
  }

  li {
    position: relative;
  }

  a {
    //color: inherit;
    color: #6c675c;
    font-weight: 500;
    font-size: 0.9rem;

    &:hover {
      text-decoration: none;
    }
  }

  &__container {
    position: relative;
    z-index: $zindex-site-header-container;
  }

  &__brand {
    margin-top: 6px;
  }

  &__upper,
  .languages {
    ul,
    li {
      display: inline-block;
    }
  }

  &__upper {
    text-align: center;
    width: 100%;
    position: fixed;
    z-index: $zindex-site-header-upper;

    @include media-breakpoint-down(sm) {
      position: static;
    }

    a {
      font-size: 0.7rem;
      padding: 0.3rem 0.5rem;
    }

    .languages {
      margin-left: 1rem;
    }
  }

  &__lower {
    //position: fixed;
    width: 100%;
    transform: translateZ(0);
    backface-visibility: hidden;
    //filter: drop-shadow(0 0 1px #333);

    @include media-breakpoint-down(sm) {
      position: static;
    }

    &-inner {
      background-color: $white;
      position: relative;
      z-index: $zindex-site-header-lower-inner;

      .navbar-bg {
        padding-top: 10px;
        padding-bottom: 10px;
        background-color: #fff;
      }
    }

    &-container {
      display: flex;
      align-items: center;

      @include media-breakpoint-up(sm) {
        @include make-container();
        @include make-container-max-widths();
      }
    }
  }

  &__nav {
    flex-grow: 1;
    display: flex;
    align-items: center;
    height: 100%;
    margin-left: 1rem;

    a {
      display: block;
      padding: 1rem 0;
      margin: 0 1rem;
      position: relative;

      &::after {
        display: block;
        background: $primary;
        width: 100%;
        position: absolute;
        height: 2px;
        bottom: 1rem;
        left: 0;
      }

      &:hover {
        &::after {
          content: "";
        }
      }
    }

    .current_page_ancestor,
    .current-menu-item {
      a::after {
        content: "";
      }
    }
  }

  &__nav-mobile {
    display: none;
  }

  .toggle-menu-button,
  .toggle-search-button {
    background-color: transparent;
    border: none;
    color: #333;
  }

  .toggle-search-button {
    margin-left: auto;
  }

  .toggle-menu-button {
    display: none;
  }

  &__search {
    transform: translateY(-100%);
    transition: 0.5s ease;
    padding: 1rem;
    background-color: $light;
    position: absolute;
    width: 100%;
    z-index: $zindex-site-header-search;

    .search-form {
      flex: 1;

      .search-field {
        font-family: $headings-font-family;
        width: 100%;
        border: none;
        background-color: transparent;
        padding: 0;
        font-size: 1.5rem;

        &:focus {
          box-shadow: none;
        }
      }

      .search-submit {
        display: none;
      }
    }

    &-wrap {
      display: flex;
    }

    .toggle-search-button {
      flex: none;
    }

    @at-root .toggled-view & {
      transform: translateY(0%);
    }
  }

  .languages {
    .active {
      font-weight: bold;
    }

    li {
      & + li {
        &::before {
          content: "\007c";
          font-weight: normal;
        }
      }

      &:last-child {
        a {
          padding-right: 0;
        }
      }
    }
  }

  .site-header--attached {
    left: 0;
    position: fixed;
    top: 0;

    @include media-breakpoint-down(sm) {
      position: static;
    }
  }

  .book-now {
    margin-left: 0.5rem;

    a {
      border-radius: 10px;
      border-radius: 35px;
      background-color: #fa7864;
      border-color: #fa7864;
      color: #fff;
      padding: 0.75rem 1.5rem;
      font-size: 1rem;

      @include media-breakpoint-down(sm) {
        padding: 0.75rem 0.5rem;
      }

      @media (max-width: 360px) {
        font-size: 0.8rem;
      }
    }
  }
}

/*
* Mobile menu
*/
.site-header__container-mobile {
  display: none;
  background-color: $light;
  padding: 15px $grid-gutter-width / 2 1rem;

  .search-form {
    margin-bottom: 1rem;

    .search-field {
      border: none;
      width: 100%;
      background-image: url(../images/icon-search.svg);
      background-repeat: no-repeat;
      background-size: 19px 26px;
      background-position: right 13px center;
    }

    .search-submit {
      display: none;
    }
  }

  .site-header__nav-mobile {
    // Only select the first list
    > ul {
      // Select all li except the first on first list
      & > li {
        padding-bottom: 6px;
        padding-top: 6px;

        & + li {
          border-top: 1px solid #c9c9c0;
        }
      }

      li > a {
        line-height: 2;
        font-size: 1rem;
        color: $textblack;
      }
    }

    // Select all lists
    ul {
      li.current_page_item {
        & > a {
          font-weight: bold;
        }
      }

      ul {
        margin-left: 1rem;

        a {
          font-size: 0.9rem;
          line-height: 1.5;
        }
      }
    }
  }

  .sub-menu-trigger {
    position: absolute;
    top: 6px;
    right: 0;
    height: 2rem;
    padding: 0 0.5rem;
    background-color: transparent;
    border: none;

    span {
      display: block;
      width: 10px;
      height: 20px;
      background: url(../images/menu-chevron-right.svg);
      background-size: cover;
      transition: 0.5s ease;
    }
  }

  .sub-menu {
    padding-bottom: 0.5rem;
    display: none;

    .sub-menu-trigger {
      height: 1.5rem;
    }
  }

  .current-menu-ancestor {
    & > .sub-menu {
      display: block;
    }

    & > .sub-menu-trigger span {
      transform: rotateZ(90deg);
    }
  }

  .current-page-ancestor,
  .sub-menu-open {
    & > .sub-menu-trigger span {
      transform: rotateZ(90deg);
    }

    & > .sub-menu {
      display: block;
    }
  }

  .languages {
    margin-top: 1rem;
  }
}

@include media-breakpoint-down(sm) {
  .site-header {
    width: 100%;
    position: static;
    top: 0;

    &__upper {
      position: revert;
    }

    &__lower {
      filter: drop-shadow(0 1px 1px rgba(51, 51, 51, 0.1));
    }

    &__nav,
    &__search,
    .toggle-search-button {
      display: none;
    }

    &__brand {
      //margin-left: ($grid-gutter-width / 2);

      img {
        max-width: 130px;
        height: auto;
      }
    }

    .toggle-menu-button {
      display: block;
      padding: 0.2rem 0.2rem;
      background-color: $primary;

      &__close {
        display: none;
      }

      @at-root .toggled-view & {
        &__burger {
          display: none;
        }

        &__close {
          display: block;
        }
      }
    }

    &__container-mobile {
      @at-root .toggled-view & {
        display: block;
      }
    }

    &__nav {
      flex-grow: unset;
      margin-left: auto;
      margin-right: unset;

      a {
        padding: 0.5rem 1rem;
        margin-left: 0;
        font-size: 0.8rem;
        color: $dark;
      }
    }

    &__nav-mobile {
      display: block;

      a {
        &:hover {
          &::after {
            content: initial;
          }
        }
      }
    }
  }

  .toggled-view {
    background-color: $light;
    padding-top: 0;

    .hero,
    .page-header,
    .page-wrapper,
    .footer {
      display: none;
    }

    .site-header {
      position: relative;
    }
  }
}

.menu-booknow {
  display: flex;
  align-items: center;

  @include media-breakpoint-down(sm) {
    .book-now {
      margin-right: 0.5rem;
    }
  }
}

.footer-nav-list {
  li {
    a {
      color: $textblack;
    }
  }
}



