.hq-ib-comparison {
  margin: 5rem 0;
  height: auto;

  .col {
    border-radius: 1rem;
    padding: 1rem 1rem 0.8rem 1rem !important;

    img {
      width: 100%;
      max-width: 160px;
      margin-left: auto;
      margin-right: auto;
      display: block;
    }

    h3 {
      font-size: 0.8rem;
      font-weight: bold;
      text-align: center;
      margin-top: 1rem;
      height: 35px;
    }
  }
}

.compare-1 {
  background: #fa7864;
  color: #fff;

  div {
    background: #fa7864;
  }
}

.compare-2 {
  background: #333;
  color: #fff;

  div {
    background: #333;
  }
}

.compare-3 {
  background: #fcf7f5;
  color: #333;

  div {
    background: #fcf7f5;
  }
}

.compare-4 {
  background: #fcf7f5;
  color: #333;

  div {
    background: #fcf7f5;
  }
}

.ct-flerp {
  top: 65px;
  position: absolute;
  left: -70px;
  transform: rotate(-90deg);
  padding: 5px 14px 0;
  border-radius: 20px 20px 0 0;
  width: 120px;
  text-align: center;
}

.ct-content-row {
  margin: 0.8rem 0;
}

span.ct-sub-title {
  font-size: 0.7rem;
  font-weight: bold;
}

span.ct-content {
  font-size: 0.7rem;
  margin-bottom: 0.5rem;
  line-height: 1.1;
}

@media (min-width: 1600px) {
  .hq-ib-comparison {
    width: 96%;

    .col {
      margin-left: 3rem !important;

      h3 {
        height: 30px;
      }
    }
  }
}

@media (max-width: 1200px) {
  section.hq-fullwidth.hq-ib-comparison {
    text-align: center;

    .hq-section {
      .row {
        .col-12 {
          margin-bottom: 20px;
        }
      }
    }
  }
}



