.hero {
  background-color: #6c6b6c;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  color: $white;
  height: 100%;
  max-height: inherit;
  margin: 0;

  &__content {
    position: relative;
    z-index: 1;

    @include media-breakpoint-up(xl) {
      margin-right: 8rem;
    }
  }

  &__title {
    span {
      display: block;
      -webkit-box-flex: 0;
      -ms-flex: none;
      flex: none;
      font-family: $playfair;
      font-size: 50px;
      line-height: 1;
      padding-bottom: 20px;
      letter-spacing: -2px;
    }
  }

  &__fullscreen-bg {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: url(/wp-content/uploads/2023/03/banner.jpeg);
    background-position: right;
    background-repeat: no-repeat;
    background-color: #e5b6bc;
    background-size: auto 100%;
    overflow: hidden;
  }

  &__fullscreen-video {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    background-position: 50%;
    background-size: cover;
    background-repeat: no-repeat;

    @media (min-width: 1000px) {
      display: block;
    }

    @include media-breakpoint-down(lg) {
      left: 60%;
    }

    @include media-breakpoint-down(md) {
      transform: translateY(-50%);
      left: 0%;
      max-width: 100%;
    }

    @include media-breakpoint-down(sm) {
      left: 50%;
      transform: translateY(-50%) translateX(-45%);
      max-width: 150%;
    }

    @include media-breakpoint-down(xs) {
      display: none;
    }
  }

  @include media-breakpoint-down(md) {
    height: 400px;
    background-image: url(../images/happident-intro-mobile.jpg);
    background-size: cover;
    background-position: center;

    &__content {
      max-width: 500px;
    }

    &__title {
      font-size: $h2-font-size;
    }
  }

  @include media-breakpoint-down(sm) {
    align-items: flex-end;
    margin-bottom: 0;

    &__text {
      display: none;
    }
  }

  @include media-breakpoint-down(xs) {
    justify-content: flex-end;
    background-image: url(/wp-content/uploads/2020/09/happident-intro-mobile-red.jpg);
    background-size: cover;
    background-position: center;
  }
}

@media screen and (max-height: 700px) {
  .hero__content {
    max-width: 100%;
  }
}

@media screen and (max-width: 1200px) {
  .hero__content {
    max-width: 100%;
  }

  .hero__fullscreen-bg {
    .hero__fullscreen-video {
      object-fit: cover;
    }
  }
}

@media (min-width: 768px) {
  .hero {
    padding-bottom: 0;
  }

  .hero-wrap-content {
    position: relative;
    z-index: 1;
    max-width: 550px;
    padding: 50px 70px;
    padding: 50px 0 0 0;
  }

  .hero-booking-bg {
    padding-bottom: 30px;
  }

  .hero-booking-wrap {
    max-width: 370px;
  }

  .hero-booking {
    .hero-booking-form {
      height: 65px;
      margin-bottom: 0;
    }

    .btn-wrap {
      width: auto;
    }

    .form-control {
      font-size: 1rem;
      height: calc(1.6em + 0.75rem + 2px);
    }
  }

  .hero-booking-txt {
    font-style: italic;
  }
}

@media (min-width: 1200px) {
  .hero__title {
    font-size: 54px;
  }

  .hero-row-content {
    ul {
      li {
        font-size: 17px;
      }
    }
  }
}

@media (min-width: 1600px) {
  .hero__content {
    margin-right: 0;
  }

  .hero__fullscreen-video {
    left: 50%;
  }
}

@media (max-width: 767.98px) {
  .hero {
    height: 100%;
  }

  .home {
    .hero {
      background-image: url(/wp-content/uploads/2020/09/happident-intro-mobile-red.jpg);
      background-position: 5%;
      border-top: 1px solid white;
    }

    .hero__fullscreen-bg {
      background-image: url(/wp-content/uploads/2023/03/banner-mobile.jpg);
    }
  }

  .hero__title {
    padding-bottom: 50px;
  }

  .hero-wrap-content {
    width: 75%;
  }

  .hero-row-content {
    ul {
      font-weight: normal;
      font-size: 12px;
    }
  }
}

@media (max-width: 420px) {
  .hero-review-quote {
    padding-left: 25px;
    padding-right: 0;
  }

  .hero-review-quote-head {
    font-size: 12px;
  }

  .hero-review-patient-stars {
    svg {
      width: 90px;
    }
  }

  .hero-review-patient-txt {
    font-size: 10px;
  }

  .hero-review-quote-latest {
    font-size: 12px;
  }

  .hero-wrap-content {
    width: 80%;
  }
}

@media screen and (max-width: 360px) {
  .home {
    .hero {
      background-position: 6%;
    }
  }
}

@media screen and (max-width: 320px) {
  .home {
    .hero {
      background-position: 18%;
    }
  }

  .hero-wrap-content {
    width: 86%;
  }
}

.hero-booking-bg {
  width: 100%;
  z-index: 99;
  background: transparent;
}

.hero-row-content {
  ul {
    font-family: Roboto, -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif;
    padding: 0;

    li {
      font-size: 16px;
      line-height: 1.4;
      font-weight: 600;
      list-style: none;
      display: flex;
      align-items: baseline;

      &::before {
        content: "";
        background-image: url(/wp-content/uploads/2020/09/white-arrow-v1.svg);
        background-repeat: no-repeat;
        width: 23px;
        height: 13px;
        -webkit-box-flex: 0;
        -ms-flex: none;
        flex: none;
      }
    }
  }
}

.hero-booking {
  width: auto;
  padding: 0;

  .row {
    height: 100%;
    align-items: flex-end;
    padding: 0 0 20px 0;
    justify-content: left;
  }

  .form-control {
    font-family: HappidentText, Georgia, Times New Roman, Times, serif;
    font-size: 0.9rem;
    border: 0;
    color: #000;
    padding: 0.3rem 0.5rem;
    background-color: rgba(255, 255, 255, 0.75);
  }

  .hero-booking-form {
    position: relative;
    height: 58px;
  }

  .btn-wrap {
    width: 100%;
  }

  .btn {
    width: 100%;
  }
}

.hero-booking-name {
  color: #fff;
  font-size: 14px;
}

.hero-booking-txt {
  font-size: 12px;
  text-align: center;
  padding: 5px 0;
  font-style: italic;
}

.hero-booking-wrap {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
}

.page-header {
  .hero {
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: $light;
    }
  }
}

@media (min-width: 1201px) {
  .hero {
    height: 500px;
  }

  .home {
    .hero {
      height: 100%;
    }
  }
}

@media (max-width: 991.98px) {
  .hero__fullscreen-video {
    left: 30%;
  }

  .site-header__nav {
    a {
      background: #fff;
      color: #000;
      padding: 7px 20px;
      border-radius: 30px;
      font-weight: 600;
    }
  }

  .site-header {
    .toggle-menu-button {
      background-color: transparent;
    }
  }
}

@media (max-width: 767.7px) {
  .hero {
    .hero-row-content {
      padding-top: 40px;
      padding-bottom: 100px;
    }

    .row {
      padding: 10px 0;
    }
  }

  .hero-booking-bg {
    background: #333;
  }

  .home {
    .hero-booking-bg {
      background: #fff;
    }
  }
}



