/** Search form */
.search-form {
  @extend .form-inline;
}

.search-form label {
  @extend .form-group;

  font-weight: normal;
}

.search-form .search-field {
  @extend .form-control;

  flex: 1;
}

.search-form .search-submit {
  @extend .btn;
  @extend .btn-primary;

  margin-left: 0.5rem;
}

.form-control {
  font-family: $font-family-sans-serif;
  font-size: 0.8rem;
}

// Customize wpcf7
.form-control.wpcf7-not-valid {
  border-color: $primary;
}

div.wpcf7-validation-errors,
div.wpcf7-acceptance-missing,
div.wpcf7-mail-sent-ok {
  border: none;
  background: $white;
}

span.wpcf7-not-valid-tip {
  display: none;
}

div.wpcf7-response-output {
  font-family: $font-family-sans-serif;
  margin: 1rem 0 0;
  padding: 0.5rem;
}



