/* stylelint-disable indentation, no-descending-specificity */

/* My pages */
.hd-mp-section {
    display: none;

    h1 {
        margin-bottom: 30px;
    }
}

#hd-mp-loading {
    display: block;
}

.hd-mp-sidebar {
    visibility: hidden;
}

/* My pages - Login */
#hd-mp-logon-ssn {
    width: 200px;
}

#hd-mp-logon-login {
    width: 200px;
    margin-top: 20px;
}

/* My pages - Appointments */
.hd-mp-col-btn {
    > div {
        display: flex;
        align-items: center;
    }

    .spinner-border {
        color: $brand-primary;
        display: none;
    }
}

#hd-mp-booking-noappointments {
    display: none;
}

.hd-mp-booking-appointment-row {
    margin: 0 0 10px 0;
    padding: 15px 0 15px 20px;
    background-color: $brand-primary-light;
    align-items: center;
    font-size: $font-size-base * 0.8;
    justify-content: space-between;

    > div:first-child > div:first-child {
        font-family: $headings-font-family;
        font-size: $font-size-base * 1.5;
    }
}

.hd-mp-col-info {
    > div {
        float: left;

        > div.hd-clinic-phone {
            line-height: 2em;
        }
    }
}

/* My pages - List */
#hd-mp-list-select,
#hd-mp-list-clinic,
#hd-mp-list-email,
#hd-mp-list-phone {
    max-width: 400px;
}

#hd-mp-list-child input.form-control,
#hd-mp-list-family input.form-control {
    width: 200px;
}

#hd-mp-list {
    label {
        font-size: $font-size-base * 0.8;
        text-transform: uppercase;
        margin-bottom: 0;
    }

    > div > div {
        margin-top: 20px;
    }

    .btn-primary {
        margin-top: 10px;
        min-width: 200px;
    }
}

#hd-mp-list-form,
#hd-mp-list-child,
#hd-mp-list-family {
    display: none;
}



