/* stylelint-disable indentation, no-descending-specificity */

/* Nav and generic */
.main {
    .btn-sm {
        font-size: 0.6rem;
        padding: 0.5rem 0.7rem;
    }

    #hd_our_clinics_title,
    #hd_our_clinics_title_pricelist {
        font-size: 2rem;
    }

    .clinics {
        margin-top: 40px;
    }

    .hd_our_clinics_nav {
        padding-top: 5px;
    }
}

.hd-m-m {
    margin-top: 10px;
    margin-bottom: 20px;
}

#hd-mp-nav {
    color: $white;
}

.hd_our_clinics_nav {
    padding-top: 30px;

    > a {
        float: right;
        margin-left: 10px;
    }
}

.hd-highlight,
.hd-clinicname,
.hd-timeslottime,
.hd-timeslotdate,
.hd-clinicianname,
.hd-treatmentname,
.hd-newtimeslottime,
.hd-newtimeslotdate {
    color: #6c675c;
    font-weight: 500;
}

.hd-timeslottime,
.hd-timeslotdate,
.hd-timeslotdateprefix,
.hd-treatmentname,
.hd-newtimeslotdateprefix {
    text-transform: lowercase;
}

