.hd-time-col-btn a {
    float: right;
}

.text-block {
    // Light
    &--theme0 {
        background-color: $light;
    }

    &.text-block-divided {
        background-color: transparent;

        > div:first-child {
            margin-right: 15px;
            display: flex;
            flex-direction: column;
        }

        > div:last-child {
            margin-left: 15px;
        }
    }
}

.hd-price {
    > div:first-child {
        font-family: $headings-font-family;
        font-size: $font-size-base * 1.5;
        line-height: 1;
    }
}



