.nav-links {
  margin-top: 2rem;
  display: flex;
  justify-content: space-between;
}

.nav-previous,
.nav-next {
  a {
    @extend .btn;
    @extend .btn-link;
    @extend .btn-sm;
  }
}

.nav-previous {
  a {
    &::before {
      content: "";
    }

    &::after {
      content: none;
    }
  }
}



