/* stylelint-disable indentation, no-descending-specificity */

/* Health */
.hd-health {
    label {
        font-size: $font-size-base;
        // text-transform: uppercase;
        margin-bottom: 0;
    }

    .btn-primary {
        margin-top: 10px;
        min-width: 200px;
    }

    .onoffswitch {
        position: relative;
        width: 80px;
        display: block;
        float: right;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
    }

    .onoffswitch-checkbox {
        display: none;
    }

    .onoffswitch-label {
        display: block;
        overflow: hidden;
        cursor: pointer;
        border: 2px solid #e3e3e3;
        border-radius: 36px;
    }

    .onoffswitch-inner {
        display: block;
        width: 200%;
        margin-left: -100%;
        transition: margin 0.3s ease-in 0s;
    }

    .onoffswitch-inner::before,
    .onoffswitch-inner::after {
        display: block;
        float: left;
        width: 50%;
        height: 36px;
        padding: 0;
        line-height: 36px;
        font-size: 13px;
        color: white;
        font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif;
        font-weight: bold;
        box-sizing: border-box;
    }

    .onoffswitch-inner::before {
        content: 'JA';
        padding-left: 12px;
        background-color: #fa7864;
        color: #fff;
    }

    .onoffswitch-inner::after {
        content: 'NEJ';
        padding-right: 12px;
        background-color: #fff;
        color: #666;
        text-align: right;
    }

    .onoffswitch-switch {
        display: block;
        width: 36px;
        margin: 0;
        background: #fff;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 44px;
        border: 2px solid #e3e3e3;
        border-radius: 36px;
        transition: all 0.3s ease-in 0s;
    }

    .onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-inner {
        margin-left: 0;
    }

    .onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-switch {
        right: 0;
    }

    .health-medic {
        margin: 10px 20px 20px 0;
    }

    .form-field {
        margin: 0 0 20px 20px;
    }

    input {
        margin: 0 5px 0 0;
    }

    .required {
        border: 2px solid red;
    }

    .health-error {
        display: none;
        margin-bottom: 10px;
    }
}



