/* stylelint-disable indentation, no-descending-specificity */

.text-block-lastminute {
    > div {
        vertical-align: top;
        padding: 20px 0;

        > div:first-child {
            text-align: left;
        }
    }
}

.hd-lastminute-row {
    &.hd-time-row {
        font-family: $headings-font-family;
        font-size: $font-size-base * 1.5;
    }
}

#hd-lastminute-cities {
    font-family: $font-family-sans-serif;
    font-size: 0.7rem;

    ul {
        padding: 0;
        list-style: none;

        li {
            display: inline-block;
            margin-right: 10px;

            a {
                color: $white;
                text-decoration: underline;
            }

            &.active a {
                text-decoration: none;
            }
        }
    }
}

#hd-lastminute-rows {
    height: 330px;
    width: 100%;
    margin: 15px 0;
    color: $black;
    overflow-x: hidden;
    overflow-y: auto;
    display: flex;
    flex-direction: column;

    > .spinner-border {
        color: $white;
        margin: auto;
    }

    > .row {
        margin: 0 0 10px 0;
        padding: 5px;
        background-color: $white;
        align-items: center;
        font-size: $font-size-base * 0.8;
        justify-content: space-between;
        min-height: 90px;

        > div:first-child {
            > div {
                white-space: nowrap;

                &:first-child {
                    font-family: $headings-font-family;
                    font-size: $font-size-base * 1;
                }
            }
        }

        > div:last-child {
            text-align: right;

            .btn-primary {
                color: $white;
                background-color: $brand-primary;
            }
        }
    }
}

.hd-lastminute-price {
    display: flex;
    align-items: center;

    > div:first-child {
        > div:first-child {
            font-family: $headings-font-family;
            font-size: $font-size-base * 0.8;
            text-decoration: line-through;
        }

        > div:last-child {
            color: red;
            font-family: $headings-font-family;
            font-size: $font-size-base * 1.5;
            line-height: 1.2;
        }
    }

    > div:last-child {
        margin-left: 10px;
        line-height: 1;
    }
}

.hd-lastminute-row .hd-lastminute-price {
    > div:first-child {
        display: flex;
        align-items: center;

        > div:first-child {
            margin-right: 10px;
        }
    }
}

.hd-summer-bumper,
.hd-lastminute-bumper {
    position: absolute;
    right: 0;
    top: -60px;

    img {
        width: 160px;
        height: 160px;
    }
}

.hd-lastminute-bumper {
    position: absolute;
    right: 290px;
    top: 10px;
}

.last-minute-loader {
    position: absolute;
    top: 0;
    left: 0;
    color: $brand-primary;
    width: 65%;
    bottom: 0;
    right: 0;
    margin: auto;
    border: 0.5em solid $brand-primary;
    border-right-color: transparent;
    border-left-color: transparent;
    padding-bottom: 65%;
    animation-duration: 1.5s;
}

.more-last-minute {
    text-align: center;
}

#home-page-last-minute-deal-banner {
    position: relative;
}



