/* stylelint-disable indentation, no-descending-specificity */

#hd_map {
    height: 100%;
    width: 100%;
    min-height: 350px;
    display: none;
    position: relative;
}

#hd_map_info {
    background-color: $brand-primary-light;
    padding: 20px;
    border-right: 10px white solid;
    display: none;
}

#hd_map_info_close {
    float: right;
}

#hd_map_container {
    margin-bottom: 10px;
    padding: 0 15px 0 15px;
}

#hd_our_clinics_title_pricelist {
    display: none;
}

#hd_map_pricelist_container {
    display: none;
}

#hd_map_pricelist_table {
    th.hd-noborder {
        padding-top: 2rem;
    }

    tr:first-child th.hd-noborder {
        border-top: none;
        padding-top: 0.75rem;
    }

    tr.hd-noborder {
        th {
            border-top: none;
            padding-top: 0;
        }
    }
}

#hd_map_col {
    padding: 0;
}

#hd_nearby_card {
    display: none;
}



