.box {
  // padding: 1.5rem;
  background-color: $light;
  margin-bottom: 1rem;
  font-size: 0.8rem;
  word-wrap: break-word;
  display: flex;
  flex-direction: column;

  &__heading {
    font-size: $h3-font-size;
    margin-top: 1rem;
    margin-bottom: 0;
    // padding: 1rem 1.5rem 0;
    // margin-bottom: 0.5rem;
  }

  &__heading,
  &__content {
    padding-right: 1rem;
    padding-left: 1rem;
    margin-bottom: 0.5rem;

    @include media-breakpoint-down(lg) {
      padding-right: 1rem;
      padding-left: 1rem;
    }
  }

  &__content {
    &:last-child {
      margin-bottom: 1rem;
    }
  }

  label,
  &__sub-heading {
    font-family: $font-family-sans-serif;
    font-size: 0.8rem;
    font-weight: bold;
    margin-bottom: 0;
  }

  p {
    font-family: $font-family-sans-serif;
    margin-bottom: 0;
  }

  a {
    text-decoration: underline;
  }
}



