.about-heading {
  padding-top: 60px;
  padding-bottom: 60px;

  @include media-breakpoint-down(sm) {
    padding-bottom: 30px;
  }

  h1 {
    font-size: 2.5rem;
    font-weight: 700;
    letter-spacing: 1px;
  }
}

.red-texture-bg {
  padding: 60px;
  background: url('../images/bg-shapes.jpg');

  @include media-breakpoint-down(sm) {
    padding: 15px;
  }

  .white-bg {
    background-color: #fff;
    padding: 45px 15px;
    text-align: center;

    h2 {
      color: $textblack;
      //font-size: 26px;
      font-weight: 700;
    }

    .width-60 {
      max-width: 600px;
      margin: auto;
    }
  }
}

.safety-section {
  padding-bottom: 80px;

  .red-texture-bg .white-bg {
    background-color: #f9f9f5;
  }

  @include media-breakpoint-down(sm) {
    padding-bottom: 40px;
  }
}

.dedicated-employee-section {
  padding-bottom: 60px;

  @include media-breakpoint-down(sm) {
    padding-bottom: 20px;
  }

  .about-quotes-section {
    padding-left: 30px;
    height: 100%;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;

    @include media-breakpoint-down(sm) {
      padding-left: 0;
      padding-top: 30px;
    }

    .about-upper-part {
      margin-bottom: 60px;

      @include media-breakpoint-down(sm) {
        margin-bottom: 30px;
      }
    }

    h6 {
      color: $textblack;
      font-size: 0.8rem;
      font-weight: 600;
      font-family: $roboto;
    }

    .quotes {
      margin-bottom: 30px;

      .fa {
        color: $textblack;
      }
    }

    h4 {
      font-weight: 600;
      letter-spacing: 1px;
    }
  }
}

.technology-clinic-section {
  padding-bottom: 60px;

  @include media-breakpoint-down(sm) {
    padding-bottom: 30px;
  }
}

.about-our-technology {
  padding-bottom: 80px;

  @include media-breakpoint-down(sm) {
    padding-bottom: 40px;
  }
}

.about-technology-image {
  padding-bottom: 80px;

  @include media-breakpoint-down(sm) {
    padding-bottom: 40px;
  }
}

.location-container {
  padding-bottom: 30px;
}

.about-map-area {
  padding-bottom: 30px;
}

.about-booknow {
  padding-top: 20px;
  padding-bottom: 60px;

  @include media-breakpoint-down(sm) {
    padding-bottom: 30px;
  }

  // .ab-text {
  //   font-size: 1.5rem;
  // }
}



