/* stylelint-disable indentation, no-descending-specificity */

/* Extending media queries */
// @media (max-width: 1199.98px)
@include media-breakpoint-down(md) {
    .hd-clinic-col {
        font-size: $font-size-base * 0.8;

        h4 {
            font-size: $font-size-base * 1.2;
        }
    }

    .hd_our_clinics_nav {
        padding-top: 0;
        padding-bottom: 10px;
    }

    .hd-clinic-details-btn a {
        margin-top: 20px;
        min-width: 0;
    }
}

@media screen and (max-width: 1200px), screen and (max-height: 700px) {
    .hd-summer-bumper {
        right: -40px;
        top: -20px;

        img {
            width: 120px;
            height: 120px;
        }
    }

    .hd-lastminute-bumper {
        right: 200px;
        top: 20px;
    }
}

// @media (max-width: 992px)
@include media-breakpoint-down(sm) {
    #hd-mp-nav {
        display: none;
    }

    .hd-summer-bumper {
        top: -60px;
        right: -40px;

        img {
            height: 120px;
            width: 120px;
        }
    }

    .hd-lastminute-bumper {
        top: 20px;
        right: 200px;

        img {
            height: 120px;
            width: 120px;
        }
    }

    .hero__title {
        padding-top: 20px;
    }

    .hd-clinic-col h4 {
        font-size: $font-size-base * 1.5;
    }

    .hd-clinic-col-btn a {
        margin-top: 10px;
    }

    .hd_our_clinics_nav {
        > a {
            float: none;
            margin-top: 10px;
        }
    }

    .hd-clinic-phone {
        margin-top: 10px;
        margin-bottom: 15px;
    }

    .site-header-mypages {
        display: none;
    }

    .text-block {
        &.text-block-divided {
            display: block;

            > div:first-child {
                margin-right: 0;
            }

            > div:last-child {
                margin-left: 0;
                margin-bottom: 30px;
            }

            > .text-block-lastminute {
                margin-bottom: 30px;
            }
        }
    }
}

// @media (max-width: 768px)
@include media-breakpoint-down(xs) {
    #hd_map_info {
        border-right: none;
        border-bottom: 10px white solid;
    }

    .hd-summer-bumper {
        top: -80px;
        right: -20px;

        img {
            height: 120px;
            width: 120px;
        }
    }

    .hd-lastminute-bumper {
        top: 20px;
        right: 80px;

        img {
            height: 120px;
            width: 120px;
        }
    }

    .hd-mp-col-info > div,
    .hd-treatment-col-btn a,
    .hd-time-col-btn a,
    .hd-clinic-col-btn a {
        margin-top: 0;
        margin-bottom: 15px;
        float: none;
    }

    .hd-mp-col-btn > div,
    .hd-mp-col-info > div {
        margin-top: 15px;
        margin-bottom: 15px;
        float: none;
    }

    .hd-mp-col-btn > div > div:last-child a {
        margin: 0 15px;
    }

    .hd-treatment-col-btn a,
    .hd-time-col-btn a {
        margin-top: 10px;
        margin-bottom: 0;
    }

    #hd-step-5 .hd-step-content > .row > div:first-child {
        padding-right: 15px;
    }

    .hd-hidden-xs {
        display: none;
    }

    .text-block-lastminute {
        > div {
            padding: 20px 0;

            #hd-lastminute-rows > .row > div:first-child > div:first-child {
                font-size: $font-size-base * 0.8;
            }

            .hd-col-btn {
                padding-left: 10px;
            }
        }
    }

    // .hd-time-row {
    //     align-items: flex-start;
    // }
}

@media screen and (max-width: 420px) {
    .hd-summer-bumper {
        top: -60px;
        right: 0;

        img {
            height: 120px;
            width: 120px;
        }
    }

    .hd-lastminute-bumper {
        top: 40px;
        right: 10px;

        img {
            height: 120px;
            width: 120px;
        }
    }
}

@media screen and (max-width: 320px) {
    .hd-summer-bumper {
        top: -60px;
        right: 0;

        img {
            height: 90px;
            width: 90px;
        }
    }

    .hd-lastminute-bumper {
        top: 40px;
        right: 10px;

        img {
            height: 90px;
            width: 90px;
        }
    }
}



