.how-it-works {
  padding-top: 100px;
  padding-bottom: 60px;

  .hiw-text {
    margin-bottom: 3rem;
  }

  .black-heading {
    font-weight: 600;
    margin-bottom: 25px;
  }

  ul {
    li {
      list-style: none;
      position: relative;
      margin-bottom: 30px;

      &::before {
        content: "";
        background: url('../../images/listing-icon.png');
        width: 23px;
        height: 23px;
        display: inline-block;
        position: absolute;
        left: -38px;
        top: 4px;
      }
    }
  }
}

.insurance-booking-card {
  .card {
    border: 0;
    background-color: #efede8;
    margin-bottom: 20px;
    padding: 25px 20px;

    .far {
      color: $textblack;
    }

    .fa {
      color: $textblack;
    }

    .book-card-text {
      margin-left: 15px;

      .uppercase {
        text-transform: uppercase;
        font-weight: 500;
      }

      h6 {
        color: $textblack;
        font-size: 0.9rem;
        margin-bottom: 0;
        text-transform: uppercase;
        font-weight: 500;
        line-height: 1.2;
        font-family: $roboto;
      }

      p {
        margin-bottom: 0;
        font-size: 0.8rem;
      }
    }

    a {
      display: flex;
      align-items: center;
      text-decoration: none;
    }

    &.active {
      background-color: $brand-primary;

      h6 {
        color: #fff;
      }

      p {
        color: #fff;
      }

      .far {
        color: #fff;
      }

      .fa {
        color: #fff;
      }
    }
  }
}



