/*
* Text section
*/
.section-text {
  margin-bottom: 2rem;
}

/*
* News section
*/
.section-news {
  $el: &;

  background-color: $light;
  padding: 4rem 2rem;
  margin-bottom: 2rem;

  a {
    color: inherit;
  }

  &__title {
    display: flex;
    font-size: 1rem;
    margin-top: 0.25rem;
    margin-bottom: 0;

    span {
      padding-right: 0.5rem;
      display: block;
      max-width: 100%;
      flex-shrink: 1;
    }

    &::after {
      content: "";
      display: block;
      background-image: url(../images/icon-arrow.svg);
      background-repeat: no-repeat;
      width: 24px;
      height: 14px;
      margin-top: 5px;
      margin-left: auto;
      transition: 0.5s ease;
    }
  }

  &__post {
    display: block;
    margin: 0 0 1rem;
    width: 100%;

    &:hover {
      text-decoration: none;

      #{$el}__title::after {
        transform: translateX(5px);
      }
    }
  }

  &__updated {
    display: block;
    font-size: 0.8rem;
    font-family: $font-family-sans-serif;
  }

  &__archive {
    text-align: right;
  }

  @include media-breakpoint-down(sm) {
    padding: 2rem 1rem;
  }
}



