// Grid system
.main {
  @include make-col-ready();
  @include make-col(12);

  margin-right: auto;
  margin-left: auto;

  @include media-breakpoint-up(lg) {
    @include make-col(9);
  }

  @include media-breakpoint-up(xl) {
    @include make-col(7);
  }

  .sidebar-primary & {
    margin-left: initial;
    margin-right: initial;
  }

  img:not(.svgicon) {
    max-width: 100%;
    height: auto;
  }
}

.sidebar {
  @include make-col-ready();

  @include media-breakpoint-up(lg) {
    @include make-col(3);
  }

  @include media-breakpoint-up(xl) {
    @include make-col(2.5);
  }
}



