.faq-wrapper {
  padding-bottom: 80px;

  @include media-breakpoint-down(sm) {
    padding-bottom: 40px;
  }

  .accordion {
    width: 100%;
    border-bottom: 1px solid #c9c9c0;
  }

  h3 {
    color: $textblack;
    font-size: 1.6rem;
    border-bottom: 3px solid #fa7864;
    padding-bottom: 10px;
    margin-top: 80px;
    font-weight: 600;
    letter-spacing: 1px;
    margin-bottom: 30px;

    @include media-breakpoint-down(sm) {
      margin-top: 30px;
    }

    &:first-child {
      margin-top: 0;
    }
  }

  h3 + .accordion {
    border-top: 1px solid #c9c9c0;
  }

  .accordion-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px 15px 25px 0;
    cursor: pointer;
  }

  .accordion-header h4 {
    margin: 0;
    color: $textblack;
    font-size: 1.2rem;
    font-weight: 400;
    letter-spacing: 1px;
    font-family: $roboto;
  }

  .accordion-content {
    display: none;

    p {
      color: $textblack;
      font-size: 1rem;
    }
  }

  .arrow {
    transition: transform 0.3s;
  }

  .arrow.open {
    transform: rotate(90deg);
  }
}



