html {
  font-size: 20px;
  // -webkit-text-stroke: 0.45px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  @include media-breakpoint-down(md) {
    font-size: 18px;
  }
}

body {
  font-family: $roboto;
  font-weight: 400;
  color: $textblack;
}

h2 {
  font-weight: 600;
  letter-spacing: 1px;
  margin-bottom: 1rem;
}

hr {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $playfair;
}

@include media-breakpoint-down(md) {
  h1,
  .h1 {
    font-size: 2rem;
    word-wrap: break-word;
    letter-spacing: 1px;
  }

  h2,
  .h2 {
    font-size: 1.5rem;
    letter-spacing: 1px;
  }

  /* stylelint-disable selector-list-comma-newline-after */
  h3,
  .h3,
  h4,
  .h4,
  h5,
  .h5,
  h6,
  .h6 {
    font-size: 1.25rem;
    letter-spacing: 1px;
  }

  .lead {
    font-size: 1.15rem;
  }
}

.table-sm {
  font-size: 0.8rem;
}

.job-list {
  ul {
    padding-left: 0;
  }

  .item {
    list-style-type: none;
    border-bottom: 1px solid #e9e9e9;
    margin-bottom: 1rem;
    padding-bottom: 1rem;
  }

  .item .wb_last_application_at,
  .item .wb_published_at,
  .item .wb_departments {
    display: none;
  }

  .item .wb_locations {
    display: block;
  }
}

.heading1 {
  font-size: 36px;
  font-weight: 700;
  line-height: 1.3;
  color: #bc8452;
  font-family: $playfair;

  @include media-breakpoint-down(sm) {
    font-size: 28px;
  }
}

.heading2 {
  margin: 0 0 50px;
  font-size: 36px;
  line-height: 1.3;
  font-weight: 400;
  color: #6c675c;
  font-family: $playfair;

  @include media-breakpoint-down(sm) {
    font-size: 24px;
    margin: 0 0 20px;
  }
}

.mb-40 {
  margin-bottom: 40px;
}

.btn-primary {
  padding-top: 12px;
  padding-bottom: 12px;
  font-size: 18px;
  font-weight: 700;
  padding-left: 2.5rem;
  padding-right: 2.5rem;
  font-family: $roboto;

  @include media-breakpoint-down(xs) {
    font-size: 12px;
  }
}

.btn-ghost {
  border: 1px solid #6c675c;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 15px;
  color: #6c675c;

  &:hover {
    background-color: #6c675c;
    color: #fff;
  }
}

p {
