  @media (min-width: 1439px) {
    font-size: 1.1rem;
    line-height: 1.3;
  }
}

.p-lr-60 {
  padding-left: 60px;
  padding-right: 60px;

  @media (max-width: 767px) {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.pl-60 {
  padding-left: 60px;

  @include media-breakpoint-down(sm) {
    padding-left: 15px;
  }
}

.pr-60 {
  padding-right: 60px;

  @media (max-width: 767px) {
    padding-right: 15px;
  }
}

.pr-0 {
  @include media-breakpoint-down(sm) {
    padding-right: 15px !important;
  }
}

.pl-0 {
  @include media-breakpoint-down(sm) {
    padding-left: 15px !important;
  }
}

.mb-100 {
  margin-bottom: 100px;

  @include media-breakpoint-down(sm) {
    margin-bottom: 30px;
  }
}

.form-group {
  .form-control {
    border-radius: 3px;
    font-size: 16px;
  }
}

a {
  color: $brand-primary;
  text-decoration: none;
  font-weight: 500;

