/* Customize the scrollbar */
::-webkit-scrollbar {
  width: 8px; /* Set the width of the scrollbar */
}

/* Customize the thumb (draggable part) */
::-webkit-scrollbar-thumb {
  background-color: #c9c9c0; /* Set the color of the thumb */
  border-radius: 4px; /* Add rounded corners to the thumb */
}

/* Customize the track (background part) */
::-webkit-scrollbar-track {
  background-color: #f1f1f1; /* Set the color of the track */
  border-radius: 4px; /* Add rounded corners to the track */
}

/* Customize the thumb on hover */
::-webkit-scrollbar-thumb:hover {
  background-color: #c4c4bc; /* Change the color of the thumb when hovered */
}

