.career-heading-wrapper {
  text-align: center;
  padding-top: 60px;
  padding-bottom: 60px;

  @include media-breakpoint-down(sm) {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  p {
    font-size: 20px;
  }
}

.two-section-image {
  @include media-breakpoint-down(sm) {
    padding-bottom: 30px;
  }

  [class*="col-"] {
    padding-left: 0;
    padding-right: 0;
  }

  .tsi-image-wrapper {
    height: 55vh;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .tsi-wrapper {
    padding: 60px;

    @include media-breakpoint-down(sm) {
      padding: 15px;
    }

    .tsi-border-bottom {
      border-bottom: 1px solid #c9c9c0;
      padding-bottom: 20px;
      font-size: 1rem;
    }

    ul {
      padding-left: 0;

      li {
        list-style: none;
        font-size: 1rem;
        margin-bottom: 10px;
      }
    }
  }
}

.inside-grey-bg {
  background-color: #efede8;
}

.inside-beige-bg {
  background-color: #f9f3ed;
}

.tsi-single-image {
  height: 100%;
}

//our mission section starts here
.our-mission {
  padding-bottom: 100px;

  @include media-breakpoint-down(sm) {
    padding-bottom: 60px;
  }

  .weight-600 {
    font-weight: 600;
    margin-bottom: 0;
  }

  h6 {
    font-size: 18px;
    line-height: 1.2;
    letter-spacing: 1px;
    font-weight: 700;
    color: $textblack;
    font-family: $roboto;
  }

  h2 {
    &.border-bottom {
      padding-bottom: 40px;
      margin-bottom: 20px;
    }
  }

  .two-part-heading {
    h6 {
      margin-bottom: 1.5rem;
      font-size: 1rem;
    }

    p {
      margin-bottom: 1.2rem;
    }
  }
}



