.text-block {
  $el: &;

  background-color: $light;
  color: $body-color;
  padding: 0;
  text-align: center;
  display: flex;

  .container {
    max-width: 850px;
  }

  &__img,
  &__text {
    flex-grow: 1;

    @include media-breakpoint-up(md) {
      flex-basis: 0;
    }
  }

  &__text {
    min-height: 450px;
    display: flex;
    justify-content: center;
    flex-direction: column;

    &-inner {
      padding: 2rem 5rem;
      width: 100%;
      max-width: 1000px;
      align-self: center;
    }
  }

  &--image {
    #{$el}__text {
      text-align: left;
    }
  }

  &__img {
    overflow: hidden;
    position: relative;
    background-size: cover;
    background-position: center;
    background-color: $dark;

    &::before {
      content: "";
      display: block;
      width: 0;
      height: 0;
      border-top: 60px solid transparent;
      border-bottom: 60px solid transparent;
      border-left: 60px solid $light;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  // Light
  &--theme0 {
    .btn {
      @extend .btn-primary;
    }
  }

  // Red
  &--theme1 {
    color: $white;
    background-color: $primary;

    #{$el}__img::before {
      border-left-color: $primary;
    }

    a:not(.btn) {
      color: $white;
      text-decoration: underline;
    }

    .btn {
      @extend .btn-white;
    }
  }

  // Dark
  &--theme2 {
    color: $white;
    background-color: #333;

    #{$el}__img::before {
      border-left-color: #333;
    }

    a:not(.btn) {
      color: $white;
      text-decoration: underline;
    }

    .btn {
      @extend .btn-white;
    }
  }

  &--quote {
    #{$el}__text {
      &-inner {
        @include media-breakpoint-up(md) {
          padding-left: 2rem;
          padding-right: 2rem;
        }
      }
    }

    &.text-block--theme0:not(.text-block--image) {
      background-color: transparent;

      #{$el}__text {
        min-height: 250px;

        &-inner {
          max-width: 1200px;
          padding-top: 4rem;
          padding-bottom: 4rem;
        }

        @include media-breakpoint-down(sm) {
          min-height: 250px;

          &-inner {
            padding-left: 0;
            padding-right: 0;
          }
        }
      }
    }

    &:not(.text-block--image) {
      @include media-breakpoint-up(lg) {
        p {
          font-size: 2.5rem;
        }
      }
    }
  }

  // Align text to the right
  &--right {
    flex-direction: row-reverse;

    @include media-breakpoint-up(md) {
      #{$el}__img::before {
        right: 0;
        transform: translateY(-50%) rotateZ(180deg);
      }
    }
  }

  @include media-breakpoint-down(lg) {
    &__text {
      min-height: 400px;

      &-inner {
        padding: 2rem;
      }
    }
  }

  @include media-breakpoint-down(md) {
    &__text {
      min-height: 300px;

      &-inner {
        padding: 2rem 1rem;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    flex-direction: column-reverse;

    &__text {
      text-align: left;
      min-height: auto;

      &-inner {
        padding: 2rem 1rem;
      }
    }

    &__img {
      width: 100%;
      height: 0;
      padding-bottom: 50%;

      &::before {
        left: 1rem;
        top: 100%;
        transform: translateY(-50%) rotateZ(-90deg);
      }
    }
  }
}



