.clinic-heading {
  hr {
    max-width: 40%;
    margin-left: unset;
  }
}

//clinic direction scss starts here
.clinic-direction {
  font-size: 0.9rem;
  padding-bottom: 60px;
}

.clinic-location-detail {
  .fas {
    color: $brand-primary-dark;
  }
}

.c-heading-container {
  .c-heading-text {
    font-size: 1rem;
    color: $textblack;
  }
}

.clinic-heading-text {
  font-size: 1rem;
  color: $textblack;
  margin-bottom: 0;
  line-height: 1;
}

.opening-hours {
  .opening-hours__day {
    margin-bottom: 5px;

    .opening-hours__day-name {
      font-weight: 700;
    }
  }
}

.clinic-team-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;

  .weight-700 {
    font-weight: 700;
  }

  span {
    width: 23px;
    height: 23px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 13px;
  }
}

//clinic oral health scss
.clinic-oral-health {
  padding-bottom: 60px;
}

.center-text {
  text-align: center;
  padding-bottom: 90px;
  padding-top: 20px;

  .black-heading {
    p {
      font-weight: normal;
      letter-spacing: 1px;
      margin-bottom: 1rem;
      font-size: 2rem;
    }
  }
}

//clinic gallery scss
.clinic-gallery {
  .gallery-section {
    .gallery-name-container {
      padding-top: 10px;
    }

    .ig-heading {
      margin-bottom: 3px;
    }
  }
}

//clinic map section scss
.clinic-map-section {
  @include media-breakpoint-down(xs) {
    margin-bottom: 30px;
  }

  .clinic-map {
    height: 400px;
  }
}

.clinic-availability {
  padding: 60px;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;

  .black-heading {
    margin-bottom: 0;
  }

  hr {
    margin-top: 0.9rem;
    margin-bottom: 0.8rem;
  }

  @include media-breakpoint-down(xs) {
    padding: 15px;
    margin-top: 30px;
  }

  ul {
    padding-left: 0;

    li {
      list-style: none;
      border-bottom: 1px solid #c9c9c0;
      padding: 10px 0;
      display: flex;
      align-items: center;
      justify-content: space-between;

      a {
        color: $textblack;
        display: block;
        text-decoration: none;
      }
    }
  }
}

.nearby-studios {
  .ns-img {
    margin-bottom: 25px;
  }

  .fas {
    color: $brand-primary-dark;
  }

  .nearby-location {
    margin-bottom: 10px;
    font-size: 0.9rem;
  }

  .nearby-number {
    font-size: 0.9rem;
  }

  h4 {
    font-weight: 600;
    letter-spacing: 1px;
  }
}

.full-width-image {
  margin-bottom: 90px;
}



