.footer {
  &__upper {
    background-color: $light;
    padding: 3rem 0;

    @include media-breakpoint-down(sm) {
      padding: 2rem 0 1rem;
    }
  }

  a {
    color: inherit;
  }

  &__legal {
    display: inline-block;
    margin-left: 2rem;
    margin-right: 2rem;

    p {
      margin-bottom: 0;
    }

    a {
      margin: 0 0.5rem;
    }
  }

  &__lower {
    padding: 0.5rem 0;
    font-size: 0.7rem;
  }

  &__languages {
    margin-bottom: 2rem;

    a {
      display: block;
      margin-bottom: 0.5rem;

      &::before {
        content: "";
        display: inline-block;
        height: 20px;
        width: 20px;
        margin-right: 0.5rem;
        background-position: center;
        vertical-align: text-top;
      }

      &.active {
        font-weight: bold;

        &::before {
          background-image: url(/wp-content/uploads/2020/09/icon-selected.svg);
        }
      }
    }
  }

  &__social-media {
    text-align: right;

    a {
      display: inline-block;
      color: #fff;

      &:not(:first-child) {
        margin-left: 1rem;
      }
    }

    @include media-breakpoint-down(lg) {
      a {
        &:not(:first-child) {
          margin-left: 0.5rem;
        }

        img {
          width: 40px;
          height: 40px;
        }
      }
    }

    @include media-breakpoint-down(sm) {
      text-align: left;
    }

    @include media-breakpoint-down(sm) {
      &-col {
        order: -1;
      }
    }
  }

  &__navigation {
    margin-bottom: 1rem;

    .nav {
      @include make-row();
    }

    .sub-menu {
      list-style: none;
      padding-left: 0;
    }

    .nav > li {
      @include make-col-ready();
      @include make-col(12);

      margin-bottom: 1rem;

      & > a {
        font-family: $headings-font-family;
        font-size: 0.9rem;
        display: block;
        margin-bottom: 0.5rem;
      }

      @include media-breakpoint-up(xs) {
        @include make-col(6);
      }

      @include media-breakpoint-up(sm) {
        @include make-col(4);
      }

      @include media-breakpoint-up(xl) {
        @include make-col(3);
      }
    }
  }
}

.newsletter-subscribe {
