  .white {
    color: #fff;
    height: 4px;
    display: block;
  }
}

.first-column {
  p {
    font-size: 16px;
    color: #fff;
  }

  @include media-breakpoint-down(md) {
    margin-bottom: 30px;
  }
}

.second-column {
  .field-group {
    margin-bottom: 10px;

    .label-name {
      font-size: 16px;
      font-weight: 700;
    }

    .label-text {
      font-size: 14px;
      font-weight: 400;
    }
  }

  @include media-breakpoint-down(md) {
    margin-bottom: 30px;
  }
}

.third-column {
  .footer-menu {
    list-style: none;
    padding-left: 0;
    display: flex;
    flex-wrap: wrap;

    & > li {
      width: 50%;
      margin-bottom: 10px;
      padding-right: 10px;
      line-height: 1.2;

      @include media-breakpoint-down(xs) {
        width: 100%;
      }

      a {
        font-size: 16px;
        color: #fff;
        font-weight: 400;
      }

      .sub-menu {
        padding-left: 0;

        li {
          list-style: none;

