body.search {
  .search-page-query-results {
    margin-bottom: 4rem;

    span {
      color: $primary;
    }
  }

  .search-page-form {
    margin-bottom: 4rem;
  }
}

.search-list {
  list-style: none;
  border-top: 1px solid #e9e9e9;

  h2 {
    font-size: 26px;
    line-height: 1.385;
    margin-bottom: 8px;
  }

  &__item {
    border-bottom: 1px solid #e9e9e9;
    padding: 40px 24px 0;
  }

  &__text {
    margin-bottom: 2rem;
  }
}



